const INITIAL_STATE = {
	profile: {},
	sessionToken: '', // a ser inclída em tds as requisições
	isAuthenticated: false,
	isLoading: false,
	error: null,
};

function user(state = INITIAL_STATE, action) {
	switch (action.type) {
		// request login to api
		case '@user/FETCH_LOGIN_USER':
			return {
				...state,
				isLoading: true,
				error: null,
			};

		// success recieve from api
		case '@user/FETCH_LOGIN_USER_SUCCESS':
			return {
				profile: action.userData.user,
				// profile: action.data.user,
				// sessionToken: action.data.token,
				isAuthenticated: true,
				isLoading: false,
				error: null,
			};

		// error recieve from api
		case '@user/FETCH_LOGIN_USER_ERROR':
			return {
				profile: {},
				sessionToken: '',
				isAuthenticated: false,
				error: action.error,
				isLoading: false,
			};

		// logout user
		case '@user/FETCH_LOGOUT_USER':
			return INITIAL_STATE;

		default:
			return state;
	}
}

export default user;

