export function streamingStart({ data, liveIds }) {
	return {
		type: '@streaming/STREAMING_START',
		data,
		liveIds,
	};
}

export function streamingStop() {
	return {
		type: '@streaming/STREAMING_STOP',
	};
}

export function streamingError(error) {
	return {
		type: '@streaming/STREAMING_ERROR',
		error,
	};
}

export function updateViewsFB(viewsFB) {
	return {
		type: '@streaming/STREAMING_UPDATE_VIEW_FB',
		viewsFB,
	};
}

export function updateViewsGL(viewsGL) {
	return {
		type: '@streaming/STREAMING_UPDATE_VIEW_GL',
		viewsGL,
	};
}
