import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { LineChart, Line, Tooltip, ResponsiveContainer } from 'recharts';

// styles
const Container = styled.div`
	position: relative;
	padding: 16px 24px 0 24px;
	background: ${({ theme }) => theme.colors.Background};
	border-radius: 5px;
	box-shadow: 0 0 5px 5px #0001;
	font-family: Poppins, sans-serif;
	.header-chart {
		display: flex;
		justify-content: space-between;
		.title-chart {
			font-weight: 600;
		}
		.views-counter {
			display: flex;
			align-items: center;
			flex-direction: column;
			span {
				font-size: 12px;
			}
			.values-counter {
				font-size: 24px;
				font-weight: 600;
			}
		}
	}
	.tooltip-chart {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 8px;
		border-radius: 5px;
		background: ${({ theme }) => theme.colors.Background};
		box-shadow: 0 0 5px 5px #0001;
		span {
			font-size: 10px;
		}
		.values-chart {
			font-size: 14px;
			font-weight: 600;
		}
	}
	hr {
		padding: 8px 24px;
		margin: 0;
	}
`;

// custom tooltip
const ViewsTooltip = ({ active, payload }) => {
	if (active && payload && payload.length) {
		return (
			<div className='tooltip-chart'>
				<span>Pessoas assistindo</span>
				<span className='values-chart'>{payload[0].value}</span>
			</div>
		);
	}
	return null;
};

// main component
export const ChartAudience = ({
	views, // from Home
	theme, // from Home
	type, // from Home (google or facebook)
}) => {
	const [audience, setAudience] = useState([]);
	useEffect(() => {
		setAudience(views);
	}, [views]);

	return (
		<Container type={type}>
			<div className='header-chart'>
				<div className='title-chart'>
					{type === 'facebook' ? 'Facebook' : 'YouTube'}
				</div>
				<div className='views-counter'>
					<span>Pessoas assistindo</span>
					<span className='values-counter'>
						{views.length > 0 ? views[views.length - 1]['live_views'] : '-'}
					</span>
				</div>
			</div>
			<ResponsiveContainer width={'99%'} height={300} minWidth={'0'}>
				<LineChart data={audience}>
					<Tooltip
						content={<ViewsTooltip />}
						isAnimationActive={false}
						offset={10}
					/>
					<Line
						dataKey='live_views'
						type='bases'
						isAnimationActive={false}
						stroke={theme.colors.Text1}
						dot={null}
						activeDot={{ fill: theme.colors.Select, r: 6 }}
					/>
				</LineChart>
			</ResponsiveContainer>
			<hr />
		</Container>
	);
};

export default ChartAudience;
