import React, { useState, useEffect, useContext } from 'react';
import { Modal, Row, Col, Spinner } from 'react-bootstrap';
import { ThemeContext } from 'styled-components';
import { useLocation } from 'react-router-dom';
import { ColorPicker, useColor } from 'react-color-palette';
import { PickerModal } from './PickerModal';
import { Title, SelectButton, Colors, SetButton } from '../styles/Styles';
import { Container } from 'react-bootstrap';

// andré: conecta ColorSection na store
import { connect } from 'react-redux';
import { resetTheme, setTheme } from '../store/modules/theme/actions';

const ColorSection = ({ dispatch, isLoading, user, theme }) => {
	//const location = useLocation();
	// const [theme, setTheme] = useState(props.Current);
	const [Select, setSelect] = useState(theme.colors.Select);
	const [Buttons1, setButtons1] = useState(theme.colors.Buttons1);
	const [Buttons2, setButtons2] = useState(theme.colors.Buttons2);
	const [Text1, setText1] = useState(theme.colors.Text1);
	const [Text2, setText2] = useState(theme.colors.Text2);
	const [Background, setBackground] = useState(theme.colors.Background);

	useEffect(() => {
		setSelect(theme.colors.Select);
		setButtons1(theme.colors.Buttons1);
		setButtons2(theme.colors.Buttons2);
		setText1(theme.colors.Text1);
		setText2(theme.colors.Text2);
		setBackground(theme.colors.Background);
	}, [theme.colors]);

	const [show1, setShow1] = useState(false);
	const [show2, setShow2] = useState(false);
	const [show3, setShow3] = useState(false);
	const [show4, setShow4] = useState(false);
	const [show5, setShow5] = useState(false);
	const [show6, setShow6] = useState(false);

	// andré: despacha eventos para a store
	const resetButtons = () => {
		const defaultColors = {
			Select: '#1664C5',
			Background: '#FFFFFF',
			Buttons1: '#24446C',
			Buttons2: 'yellow',
			Text1: '#121212',
			Text2: '#FFFFFF',
		};
		// console.log(defaultColors);
		dispatch(setTheme(defaultColors, user.id));
	};

	const setNewTheme = () => {
		const newTheme = {
			Select,
			Buttons1,
			Buttons2,
			Text1,
			Text2,
			Background,
		};
		// console.log(newTheme);
		dispatch(setTheme(newTheme, user.id));

		// setTheme({
		// 	colors: {
		// 		Select: Select,
		// 		Buttons1: Buttons1,
		// 		Buttons2: Buttons2,
		// 		Text1: Text1,
		// 		Text2: Text2,
		// 		Background: Background,
		// 	},
		// });
		// console.log('Set Theme called');
		// console.log(theme);
		// props.updateTheme({
		// 	colors: {
		// 		Select: Select,
		// 		Buttons1: Buttons1,
		// 		Buttons2: Buttons2,
		// 		Text1: Text1,
		// 		Text2: Text2,
		// 		Background: Background,
		// 	},
		// });
	};

	return (
		<>
			<Container>
				<Colors>
					<SetButton
						style={{ backgroundColor: Select }}
						className={'color-block'}
						onClick={() => {
							setShow1(true);
							console.log('pressed');
						}}></SetButton>
					<SetButton
						style={{ backgroundColor: Buttons1 }}
						className={'color-block'}
						onClick={() => {
							setShow2(true);
							console.log('pressed');
						}}></SetButton>
					<SetButton
						style={{ backgroundColor: Buttons2 }}
						className={'color-block'}
						onClick={() => {
							setShow3(true);
							console.log('pressed');
						}}></SetButton>
					<SetButton
						style={{ backgroundColor: Text1 }}
						className={'color-block'}
						onClick={() => {
							setShow4(true);
							console.log('pressed');
						}}></SetButton>
					<SetButton
						style={{ backgroundColor: Text2 }}
						className={'color-block'}
						onClick={() => {
							setShow5(true);
							console.log('pressed');
						}}></SetButton>
					<SetButton
						style={{ backgroundColor: Background }}
						className={'color-block'}
						onClick={() => {
							setShow6(true);
							console.log('pressed');
						}}></SetButton>
				</Colors>

				<Container className='home-buttons-cont'>
					<SelectButton onClick={setNewTheme}>Atualizar</SelectButton>
					<SelectButton onClick={resetButtons}>Padrão</SelectButton>
				</Container>
			</Container>
			<PickerModal
				show={show1}
				onHide={() => setShow1(false)}
				updateColor={setSelect}
			/>
			<PickerModal
				show={show2}
				onHide={() => setShow2(false)}
				updateColor={setButtons1}
			/>
			<PickerModal
				show={show3}
				onHide={() => setShow3(false)}
				updateColor={setButtons2}
			/>
			<PickerModal
				show={show4}
				onHide={() => setShow4(false)}
				updateColor={setText1}
			/>
			<PickerModal
				show={show5}
				onHide={() => setShow5(false)}
				updateColor={setText2}
			/>
			<PickerModal
				show={show6}
				onHide={() => setShow6(false)}
				updateColor={setBackground}
			/>
		</>
	);
};

const mapStateToProps = (state) => ({
	user: state.user.profile,
	theme: state.theme,
	isLoading: state.theme.isLoading,
});

export default connect(mapStateToProps)(ColorSection);
