/* eslint-disable eqeqeq */
import React, { useState, useRef, useEffect } from "react";
import { Modal } from "react-bootstrap";
import InputMask from "react-input-mask";
import "./MyCenteredModal.css";
import * as api from "../Services/api";

export const MyCenteredModal = (props) => {
  const [day, setDay] = useState(null);
  const [month, setMonth] = useState(null);
  const [year, setYear] = useState(null);

  const Ref1 = useRef();
  const Ref2 = useRef();

  const changeFocusDia = (value) => {
    if (value) {
      console.log("value->", value);
      if (value > 0) {
        Ref1.current.getInputDOMNode().focus();
      }
    }
  };

  const changeFocusMes = (value) => {
    if (value) {
      if (value > 0) {
        Ref2.current.getInputDOMNode().focus();
      }
    }
  };

  const handleSubmit = async (formdata, id) => {
    const data = formdata;
    console.log("data being passed->", data);
    const response = await api.editUser(data, id);
    if (!response.error) {
      console.log("response in Management->", response);
      if (response) {
        props.onHide();
        //mensagem de sucesso
      }
    } else {
      console.log(response.error.message);
      props.onHide();
      //mensagem de erro
    }
  };

  // useEffect(() => {
  //   if (day) {
  //     if (day == "00") setDay("");
  //     if (day.search("_") == -1) {
  //       let num = parseInt(day, 10);
  //       if (!isNaN(num)) {
  //         if (num >= 0 && num <= 31) {
  //           let value = num.toString();
  //           changeFocusDia(value);
  //         } else setDay("");
  //       }
  //     }
  //   }
  // }, [day]);

  // useEffect(() => {
  //   if (month) {
  //     if (month == "00") setMonth("");
  //     if (month.search("_") == -1) {
  //       let num = parseInt(month, 10);
  //       if (!isNaN(num)) {
  //         if (num >= 0 && num <= 12) {
  //           let value = num.toString();
  //           changeFocusMes(value);
  //         } else setMonth("");
  //       }
  //     }
  //   }
  // }, [month]);

  return (
    <Modal
      dialogClassName="modal-40w"
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <form
          className="Form-contrato"
          autocomplete="off"
          onSubmit={(event) => {
            event.preventDefault();
            const data = `${day}/${month}/${year}`;
            const formdata = new FormData();
            formdata.append("renovar", data);
            handleSubmit(formdata, props.UserInfo.id);
            console.log(data);
            setDay("");
            setMonth("");
            setYear("");
          }}
        >
          <h1 className="title-contrato">Renovar Contrato</h1>
          <div className="input-container">
            <div className="grouping">
              <label className="labels-contrato" htmlfor="nome">
                Dia
              </label>
              <InputMask
                mask="99"
                name="dia"
                id="dia-field"
                value={day}
                required
                className="inputs-contrato"
                placeholder="dd"
                maskPlaceholder={null}
                alwaysShowMask={false}
                onChange={(event) => {
                  setDay(event.target.value);
                }}
              ></InputMask>
            </div>
            <div className="grouping">
              <label className="labels-contrato" htmlfor="email">
                Mes
              </label>
              <InputMask
                mask="99"
                id="mes-field"
                name="mes"
                value={month}
                required
                className="inputs-contrato"
                placeholder="mm"
                maskPlaceholder={null}
                alwaysShowMask={false}
                ref={Ref1}
                onChange={(event) => {
                  setMonth(event.target.value);
                }}
              ></InputMask>
            </div>
            <div className="grouping">
              <label className="labels-contrato">Ano</label>
              <InputMask
                mask="9999"
                name="ano"
                id="ano-field"
                value={year}
                required
                className="inputs-contrato"
                placeholder="aaaa"
                maskPlaceholder={null}
                alwaysShowMask={false}
                ref={Ref2}
                onChange={(event) => {
                  setYear(event.target.value);
                }}
              ></InputMask>
            </div>
          </div>

          <input type="submit" className="login-button"></input>
        </form>
      </Modal.Header>
    </Modal>
  );
};
