import React, { useState } from 'react';
import styled from 'styled-components';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';

// main component
/**
 *
 * @param {} props
 * @returns {number} microphone
 * @returns {number} audio
 */
export default function AudioControl(props) {
	const [mic, setMic] = useState(0.5);
	const [micDisabled, setMicDisabled] = useState(false);
	const [audio, setAudio] = useState(0.5);
	const [audioDisabled, setAudioDisabled] = useState(false);

	const toggleMic = (selected) => {
		if (selected) {
			setMic(0.5);
			setMicDisabled(false);
			props.microphone(0.5);
		} else {
			setMic(0);
			setMicDisabled(true);
			props.microphone(0);
		}
	};

	const toggleAudio = (selected) => {
		if (selected) {
			setAudio(0.5);
			setAudioDisabled(false);
			props.audio(0.5);
		} else {
			setAudio(0);
			setAudioDisabled(true);
			props.audio(0);
		}
	};
	return (
		<Container>
			<div className='audio-title'>Áudio</div>
			<div>
				<div className='audio-control'>
					<span>Microfone</span>
					<div>
						<Microphone onClick={toggleMic} />
						<Slider
							value={mic}
							disabled={micDisabled}
							step={0.01}
							min={0}
							max={1}
							onChange={(e) => {
								const n = Number(e.target.value);
								setMic(n);
								props.microphone(n);
							}}
						/>
					</div>
				</div>
				<div className='audio-control'>
					<span>Áudio da Transmissão</span>
					<div>
						<Audio onClick={toggleAudio} />
						<Slider
							value={audio}
							disabled={audioDisabled}
							step={0.01}
							min={0}
							max={1}
							onChange={(e) => {
								const n = Number(e.target.value);
								setAudio(n);
								props.audio(n);
							}}
						/>
					</div>
				</div>
			</div>
		</Container>
	);
}

// styles
const Container = styled.div`
	padding: 16px 15px 16px 60px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	color: ${({ theme }) => theme.colors.Text1};
	font-family: Poppins, sans-serif;
	font-weight: 600;
	width: 100%;
	.audio-title {
		font-size: 20px;
		padding-bottom: 16px;
	}
	> div {
		display: flex;
		width: 100%;
		.audio-control {
			display: flex;
			flex-direction: column;
			font-size: 16px;
			margin-right: 32px;
			width: 100%;
			> span {
				margin-bottom: 12px;
			}
			> div {
				display: flex;
				align-items: center;
				background: #eee;
				border-radius: 5px;
				padding: 8px;
			}
		}
	}
	@media (max-width: 768px) {
		padding: 16px 0 !important;
		> div {
			flex-direction: column;
			.audio-control {
				margin-right: 0;
				margin-bottom: 16px;
			}
		}
	}
`;

const Button = styled.div`
	cursor: pointer;
	padding: 4px;
`;

const Slider = styled.input.attrs({ type: 'range' })`
	padding: 0;
	margin: 0;
	width: 100%;
	height: auto;
	background: transparent;
	border: none;
	&::-webkit-slider-thumb {
		cursor: pointer;
		margin-top: -4px;
		height: 4px;
		width: 4px;
	}
	&::-webkit-slider-runnable-track {
		cursor: pointer;
		height: 6px;
		border-radius: 3px;
		background: #ddd;
	}
	&::-ms-fill-lower {
		background: #333 !important;
		border-radius: 3rem;
	}
	/* firefox */
	&::-moz-range-thumb {
		cursor: pointer;
		margin-top: -4px;
		height: 4px;
		width: 4px;
	}
	&::-moz-range-track {
		cursor: pointer;
		height: 6px;
		border-radius: 3px;
		background: #ddd;
	}
`;

// control mic
const Microphone = (props) => {
	const [select, setSelect] = useState(false);

	const toggleSelect = () => {
		setSelect(!select);
		props.onClick(select);
	};

	return (
		<OverlayTrigger
			placement='bottom-start'
			overlay={
				<Tooltip style={{ fontFamily: 'Poppins' }}>
					{select ? 'Ativar microfone' : 'Desativar microfone'}
				</Tooltip>
			}>
			<Button onClick={toggleSelect}>
				{select ? <SvgMicrophoneOff /> : <SvgMicrophone />}
			</Button>
		</OverlayTrigger>
	);
};

// control audio
const Audio = (props) => {
	const [select, setSelect] = useState(false);

	const toggleSelect = () => {
		setSelect(!select);
		props.onClick(select);
	};

	return (
		<OverlayTrigger
			placement='bottom-start'
			overlay={
				<Tooltip style={{ fontFamily: 'Poppins' }}>
					{select ? 'Ativar áudio' : 'Desativar áudio'}
				</Tooltip>
			}>
			<Button onClick={toggleSelect}>
				{select ? <SvgAudioOff /> : <SvgAudio />}
			</Button>
		</OverlayTrigger>
	);
};

