const INITIAL_STATE = {
	socket: null,
	listErrors: [
		// {
		// 	type: 'audio',
		// 	titleError: 'Falha no sistema de audio.',
		// 	msgError: 'Houve uma falha durante a transmissão de audio.',
		// 	issued_At: new Date(),
		// },
	],
};

const errors = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case '@errors/CONNECT_ERRORS':
			return {
				...state,
				socket: action.socket,
			};

		case '@errors/DISCONNECT_ERRORS':
			return {
				...state,
			};

		case '@errors/ADD_ERROR':
			const date = new Date();
			const listErrors = [...state.listErrors];
			action.error['issued_At'] = date;
			listErrors.push(action.error);
			return {
				...state,
				listErrors,
			};

		default:
			return state;
	}
};

export default errors;
