import React, { useState } from 'react';
import './Landing.css';
import { MyCarousel } from '../components/Carousel';
import Navbar from '../components/Navbar';
import { MyButton } from '../components/MyButton';
import icon1 from '../assets/icon-1.png';
import icon2 from '../assets/icon-2.png';
import mockup from '../assets/mockup.png';
import Logo from '../assets/Logo.png';
import { Container, Row, Col, Alert } from 'react-bootstrap';
import ReactInputMask from 'react-input-mask';
import * as api from '../Services/api';

export const Landing = (props) => {
	const [variantAlert, setVariantAlert] = useState('');
	const [messageAlert, setMessageAlert] = useState('');
	const [showAlert, setShowAlert] = useState(false);
	const [name, setName] = useState('');
	const [email, setEmail] = useState('');
	const [phone, setPhone] = useState('');
	const [msg, setMsg] = useState('');

	// validating email
	const validateEmail = (email) => {
		const re =
			/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return re.test(String(email).toLowerCase());
	};

	// check fileds
	const verifyContact = () => {
		if (!name || name.length < 3) return false;
		if (!email || !validateEmail(email)) return false;
		if (!phone) return false;
		if (!msg) return false;
		return true;
	};

	// send message
	const sendMessage = async () => {
		if (!verifyContact()) {
			return toggleAlert(true, 'Por favor, preencha todos os campos abaixo.');
		}
		const data = {
			name: name,
			'e-mail': email,
			phone: phone,
			message: msg,
		};
		const res = await api.sendMessageForm(data);
		console.log(res);
		if (res) {
			setName('');
			setEmail('');
			setPhone('');
			setMsg('');
			return toggleAlert(false, 'Mensagem enviada com sucesso!');
		}
		return toggleAlert(true, 'Erro ao enviar a mensagem.');
	};

	const toggleAlert = (error, message) => {
		if (message) {
			setVariantAlert(error ? 'danger' : 'success');
			setMessageAlert(message);
			setShowAlert(true);
		} else {
			setShowAlert(false);
		}
	};

	return (
		<Container className='ct-ld-page' fluid>
			<Navbar type='navbar' />
			<Row id='home'>
				<Col xl={5} className='nl-tl'>
					<div className='box1'>
						<div className='bar-effect' />
						<h1 className='title-1'>
							LIVE PROFISSIONAL <br /> PARA O LEGISLATIVO!
						</h1>
						<h2 className='title-2'>
							Com um software de última geração, a Câmara de Vereadores agora
							pode transmitir suas reuniões em redes sociais e outros canais da
							internet, com qualidade HD (High Definition), sem precisar fazer
							nenhum investimento em equipamentos e sistemas, pagando apenas um
							baixo valor mensal pela locação.
						</h2>
						<MyButton buttonSize='btn--large'>
							É novo por aqui? Faça seu orçamento
							<i class='fas fa-arrow-right'></i>
						</MyButton>
					</div>
				</Col>
				<Col xl={7} className='nl-tr'>
					<MyCarousel />
				</Col>
			</Row>
			<Row id='about-us'>
				<Col xl={6} className='nl-ml'>
					<div className='box2'>
						<h1 className='title-3'>
							SESSÕES COM PÚBLICO ONLINE E TRANSPARÊNCIA NOS ATOS
						</h1>
						<h2 className='title-4'>
							Consolidando sua qualidade e credibilidade de quase três décadas,
							o Jornal da Fronteira, através do LEGISLATIVO TV, avança com um
							projeto de LIVESTREAMING de alto padrão de qualidade em som e
							imagem. O Poder Legislativo nunca esteve tão perto do cidadão.
						</h2>
						<h2 className='title-5'>
							Basta contratar o serviço de transmissão de sessões e reuniões do
							Legislativo TV, para levar informações com qualidade e eficiência
							para toda a sua comunidade.
						</h2>
					</div>
					<img src={icon2} className='icon-1' alt='icone-camera'></img>
				</Col>
				<Col xl={6} className='nl-mr'>
					<img src={icon1} className='icon-2' alt='icone-monitor'></img>
					<img src={mockup} className='mockup' alt='imagem-mockup'></img>
				</Col>
			</Row>
			<Row id='contact'>
				<Col xl={12} className='nl-bt'>
					<div className='title-6-container'>
						<h1 className='title-6'>
							A TECNOLOGIA DEVE ESTAR A SERVIÇO DA CÂMARA DE VEREADORES, PARA
							QUE POSSA ESTAR PRÓXIMA DA POPULAÇÃO E LEVAR INFORMAÇÃO E
							TRANSPARÊNCIA. <br /> OBTENHA UM ORÇAMENTO POR UM DOS CANAIS
							ABAIXO
						</h1>
					</div>
					<div className='bt-form'>
						<Row className='form-row'>
							<Col className='form-card-left'>
								<div className='grouping'>
									<label className='labels' htmlfor='nome'>
										Nome
									</label>
									<input
										type='text'
										name='nome'
										className='text-input'
										placeholder='Qual seu nome?'
										value={name}
										onChange={(e) => setName(e.target.value)}
									/>
								</div>
								<div className='grouping'>
									<label className='labels' htmlfor='email'>
										E-mail
									</label>
									<input
										type='email'
										name='email'
										className='text-input'
										placeholder='Digite seu e-mail principal'
										value={email}
										inputMode='email'
										onChange={(e) => setEmail(e.target.value.trim())}
									/>
								</div>
								<div className='grouping'>
									<label className='labels'>Telefone</label>
									<ReactInputMask
										mask='(99) 9999-9999'
										type='text'
										name='phone'
										className='text-input'
										placeholder='Qual seu telefone para contato?'
										value={phone}
										onChange={(e) => setPhone(e.target.value.trim())}
									/>
								</div>
							</Col>
							<Col className='form-card-right'>
								<div className='grouping'>
									<label className='labels'>Mensagem</label>
									<textarea
										placeholder='Deixe a sua mensagem!'
										value={msg}
										onChange={(e) => setMsg(e.target.value)}
									/>
									{/* <input type='submit' className='submit'></input> */}
									<button className='submit' onClick={sendMessage}>
										Enviar
									</button>
								</div>
							</Col>
						</Row>
						{/* alert error / success */}
						<Alert
							className='alert-contact'
							variant={variantAlert}
							onClose={() => toggleAlert()}
							dismissible
							show={showAlert}>
							<p>{messageAlert}</p>
						</Alert>
					</div>
				</Col>
			</Row>
			<Row>
				<Col xl={4} className='nl-ft1'>
					<img alt='footer logo' src={Logo}></img>
				</Col>
				<Col xl={4} className='nl-ft2'>
					<h3 className='footer-title1'>Sobre nós</h3>
					<p className='footer-title2'>
						O Legislativo TV, que é uma empresa do Jornal da Fronteira, meio de
						comunicação com quase 30 anos de atuação, conhece a realidade do
						setor público, principalmente a atuação do vereador, que é o
						porta-voz da população.
					</p>
				</Col>
				{/* <Col xl={3} className="nl-ft3">
          <h3 className="footer-title1">Nos acompanhe</h3>
          <div className="social-buttons">
            <a href="https://www.facebook.com/Legislativo-TV-101050395459102">
              <i class="fab fa-facebook fa-2x" />
            </a>
            <a href="https://www.instagram.com/">
              <i class="fab fa-instagram fa-2x" />
            </a>

            <a href="https://www.youtube.com/">
              <i class="fab fa-youtube fa-2x" />
            </a>
          </div>
        </Col> */}
				<Col xl={4} className='nl-ft4'>
					<h3 className='footer-title1'>Entre em contato</h3>
					<h4 className='footer-title2'>(49) 3644 – 1724</h4>
					<MyButton
						buttonStyle='btn--footer'
						buttonSize='button--footer'
						onClick={() =>
							(window.location.href =
								'https://api.whatsapp.com/send?phone=5549984090431')
						}>
						<i class='fab fa-whatsapp'></i>
						Mande uma mensagem!
					</MyButton>
				</Col>
			</Row>
		</Container>
	);
};
