import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faTrashAlt, faTimes } from '@fortawesome/free-solid-svg-icons';
import { Spinner, Modal } from 'react-bootstrap';

// styles
const Container = styled.div`
	user-select: none;
	width: 220px;
	background: ${({ theme }) => theme.colors.Background};
	margin-right: 48px;
	@media (max-width: 768px) {
		width: 100%;
		margin-right: 0;
	}
`;

const Header = styled.div`
	height: 65px; // normalize header
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 16px 0;
	span {
		font: normal normal 600 16px/25px Poppins;
		color: ${({ theme }) => theme.colors.Text1};
	}
	/* add style button */
	button {
		border: none;
		outline: none;
		padding: 4px 8px;
		border-radius: 5px;
		background: #e9e9e9;
		&:disabled {
			cursor: normal;
			svg {
				opacity: 0.4;
			}
		}
	}
`;

const ListView = styled.div`
	display: flex;
	flex-direction: column;
	max-height: 250px;
	overflow-y: scroll;
	padding-right: 8px;
	svg {
		fill: ${({ theme }) => theme.colors.Text1};
	}
`;

const Thumbnail = styled.div`
	display: flex;
	flex-direction: column;
	cursor: pointer;
	border-radius: 5px;
	/* overflow: hidden; */
	background-color: #e9e9e9;
	transition: transform ease 0.2s;
	margin-bottom: 8px;
	padding: 8px;
	border: ${({ selected, theme }) =>
		selected
			? `solid 2px ${theme.colors.Select}`
			: `solid 2px ${theme.colors.Background}`} !important;
	font-family: 'Poppins', sans-serif;
	color: ${({ theme }) => theme.colors.Text1};
	span {
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	> span.title-subs {
		font-size: 16px;
	}
	> span.desc-subs {
		font-size: 12px;
		opacity: 0.4;
	}
	&:hover {
		/* box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.25); */
		/* transform: scale(1.025); */
	}
`;

const NewSubtitleButton = styled(Thumbnail)`
	min-height: 62px;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	border: none;
	span {
		font-size: 14px;
		font-weight: 600;
	}
	svg {
		fill: ${({ theme }) => theme.colors.Text1};
	}
`;

const Button = styled.button`
	display: flex;
	align-items: center;
	background: #e9e9e9;
	width: 100%;
	min-height: 50px;
	margin: 30px 0;
	border-radius: 10px;
	justify-content: center;
	font: normal normal 500 16px/25px Poppins;
	letter-spacing: 0px;
	color: ${({ theme }) => theme.colors.Text1};
	border: none;
	outline: none;
	height: 40px;
	svg {
		margin-right: 6px;
	}
	&:disabled {
		filter: opacity(0.5);
	}
`;

const Form = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: stretch;
	align-items: center;
	font-family: Poppins, sans-serif;
	color: ${({ theme }) => theme.colors.Text1};

	.closebox {
		width: 100%;
		display: flex;
		align-items: flex-end;
		padding: 8px 16px;
		svg {
			cursor: pointer;
			font-size: 24px;
			fill: ${({ theme }) => theme.colors.Text1};
		}
	}

	.header {
		padding: 20px 0 40px 0;
		align-items: center;
		font-size: 18px;
		font-weight: 600;
	}

	div {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		padding-bottom: 24px;
		padding: 0 40px 24px 40px;
		@media (max-width: 768px) {
			padding: 0 15px 12px 15px;
		}
		span {
			font-size: 14px;
			font-weight: 600;
		}
	}

	input {
		outline: none;
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		width: 100%;
		height: 54px;
		padding-left: 20px;
		padding-right: 20px;
		background: #ffffff 0% 0% no-repeat padding-box;
		border: 2px solid #e9e9e9;
		border-radius: 10px;
		opacity: 1;
		text-align: left;
		font: normal normal normal 16px/25px Poppins;
		letter-spacing: 0px;
		/* color: #cccccc; */
		&::placeholder {
			color: #cccccc;
		}
	}
`;

// main component
export const CardSubtitlesThumbnail = ({
	title,
	items,
	isLoading,
	deleteButton,
	onDeleteSubtitles,
	onAddNewSubtitle,
}) => {
	const [subs, setSubs] = useState([]);
	const [selectedItems, setSelectedItems] = useState([]);
	const [newTitle, setNewTitle] = useState('');
	const [newSubtitle, setNewSubtitle] = useState('');
	const [showModal, setShowModal] = useState(false);

	useEffect(() => {
		if (!items.length) return;
		const arr = [];
		items.map((ele) =>
			arr.push({
				...ele,
				selected: ele.selected || false,
			})
		);
		setSubs(arr);
	}, [items]);

	const toggleSelect = (e, ele) => {
		const { id, selected } = ele;
		const selItems = selectedItems;
		setSubs(
			subs.map((item) =>
				item.id === id ? { ...item, selected: !selected } : item
			)
		);

		if (!selected) {
			// inverso do state
			selItems.push(id);
		} else {
			const idx = selItems.findIndex((p) => p === id);
			selItems.splice(idx, 1);
		}
		setSelectedItems(selItems);
	};

	const deleteSubtitles = (e, arrSubs) => {
		onDeleteSubtitles(arrSubs);
		setSelectedItems([]);
	};

	const addSubtitle = (e, newTitle, newSubtitle) => {
		onAddNewSubtitle(newTitle, newSubtitle);
		setNewTitle('');
		setNewSubtitle('');
		setShowModal(false);
	};

	const onChangeNewTitle = (e) => {
		setNewTitle(e.target.value);
	};

	const onChangeNewSubtitle = (e) => {
		setNewSubtitle(e.target.value);
	};

	const onHandlerModal = () => {
		setShowModal(!showModal);
	};

	return (
		<Container>
			<Header>
				<span>{title}</span>
				{deleteButton ? (
					<button
						disabled={!selectedItems.length}
						onClick={(e) => deleteSubtitles(e, selectedItems)}>
						<FontAwesomeIcon icon={faTrashAlt} />
					</button>
				) : null}
			</Header>
			<ListView>
				{isLoading ? (
					<Spinner animation='border' size='sm' />
				) : (
					subs?.map((ele) => (
						<Thumbnail
							key={ele.id}
							selected={ele.selected}
							onClick={(e) => toggleSelect(e, ele)}>
							<span className='title-subs'>{ele.title}</span>
							<span className='desc-subs'>{ele.subtitle}</span>
						</Thumbnail>
					))
				)}
				<NewSubtitleButton onClick={onHandlerModal}>
					<FontAwesomeIcon icon={faPlus} />
					<span>Adicionar Legenda</span>
				</NewSubtitleButton>
			</ListView>
			{/* <ListView>
				<NewSubtitleButton onClick={onHandlerModal}>
					<FontAwesomeIcon icon={faPlus} />
					<span>Adicionar Legenda</span>
				</NewSubtitleButton>
			</ListView> */}
			<Modal centered show={showModal} onHide={onHandlerModal}>
				<Modal.Body>
					<Form>
						<div className='closebox'>
							<FontAwesomeIcon icon={faTimes} onClick={onHandlerModal} />
						</div>
						<div className='header'>Adicionar legenda</div>
						<div>
							<span>Título</span>
							<input
								type='text'
								value={newTitle}
								placeholder='Título'
								onChange={onChangeNewTitle}
							/>
						</div>
						<div>
							<span>Subtítulo</span>
							<input
								input
								value={newSubtitle}
								type='text'
								placeholder='Subtítulo'
								onChange={onChangeNewSubtitle}
							/>
						</div>
						<div>
							<Button
								disabled={!newTitle || !newSubtitle}
								onClick={(e) => addSubtitle(e, newTitle, newSubtitle)}>
								<span>Adicionar</span>
							</Button>
						</div>
					</Form>
				</Modal.Body>
			</Modal>
		</Container>
	);
};

CardSubtitlesThumbnail.protoTypes = {
	title: PropTypes.string,
	items: PropTypes.array,
	isLaoding: PropTypes.bool,
	deleteButton: PropTypes.bool,
	onDeleteSubtitles: PropTypes.func,
	onAddNewSubtitle: PropTypes.func,
};

export default CardSubtitlesThumbnail;
