import React, { useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import styled, { css } from 'styled-components';

const Container = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;

	/* variant to fix user modal */
	${({ variant }) => {
		switch (variant) {
			case 'text-button':
				return css`
					width: auto;
					input,
					button {
						padding: 8px 32px;
						svg {
							margin-left: 8px !important;
						}
					}
				`;

			default:
				return css`
					width: 60px;
				`;
		}
	}};
	height: 60px;
	border-radius: 5px;
	overflow: hidden;
	transition: opacity, transform ease 0.2s;
	input,
	button {
		background-color: #e9e9e9;
		border: none;
		outline: none;
		width: 100%;
		height: 100%;
	}
	&:hover {
		/* box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.25); */
		/* transform: scale(1.025); */
	}
	&:active {
		transform: none;
	}
`;

export const FileUploader = (props) => {
	const fileInput = useRef(null);

	const handleFileInput = (e) => {
		// handle validations
		const file = e.target.files[0];
		if (file?.size > props.sizeLimit) {
			const limitMB = props.sizeLimit / 1048576;
			const error =
				'O tamanho do arquivo não pode ser maior do que ' +
				limitMB.toString() +
				'MB';
			props.onFileSelectError(error);
		} else {
			props.onFileSelectSuccess(file);
		}

		// console.log("file size-> ", file.size);
		// console.log("file name-> ", file.name);
		// console.log("file-> ", file);
	};

	return (
		<Container variant={props.variant}>
			<input
				type='file'
				onChange={handleFileInput}
				style={{ display: 'none' }}
				ref={fileInput}
				accept={props.types} // adiciona prop ao input
			/>
			<button
				type='button'
				onClick={(e) => fileInput.current && fileInput.current.click()}
				className={props.cName}>
				{props.text}
				<FontAwesomeIcon
					icon={faPlus}
					style={{ color: 'black', margin: '0' }}
				/>
			</button>
		</Container>
	);
};
