import Axios from 'axios';

import config_prd from '../legislativotv-config.json';
import config_dev from '../local-config.json';
// trata erro do local-config
const config = process.env.NODE_ENV === 'development' ? config_dev : config_prd;

export const cfg = config;

Axios.defaults.withCredentials = true;

Axios.defaults.headers = {
	"Access-control-allow-origin": "*"
}

export const login = async (data) => {
	try {
		// laravel necessita de credentials
		// withCredencials define o set-cookie no axios
		// fetch csrf token from a url endpoint
		Axios.defaults.withCredentials = true;
		const csrf = await Axios.get(config.BASE_URL + '/sanctum/csrf-cookie');
		// verifica se o laravel enviou o csrf token
		if (csrf.status >= 200) {
			// `xsrfCookieName` is the name of the cookie to use as a value for xsrf token
			Axios.defaults.xsrfCookieName = 'XSRF-TOKEN';
			// renomeia o set-cookie no header do axios
			// `xsrfHeaderName` is the name of the http header that carries the xsrf token value
			Axios.defaults.xsrfHeaderName = 'X-XSRF-TOKEN';
			const response2 = await Axios.post(config.BASE_URL + '/login', data);
			if (!response2.data) {
				throw Error('Não foi possivel obter os dados do servidor.');
			}
			if (response2.data.error) {
				throw Error(response2.data.error);
			}
			return response2.data;
		} else {
			Axios.defaults.headers = {};
		}
	} catch (error) {
		return { error: error };
	}
};

