// reset password
export const resetPassword = async (email) => {
	try {
		const res = await Axios.post(config.API_URL + '/users/recover', {
			'e-mail': email,
		});
		if (res.status === 200) return true;
		return false;
	} catch (error) {
		console.log(error);
		return false;
	}
};

//adiciona Camera ao Servidor Live
export const addCam = async (data) => {
	try {
		const res = await Axios.post(data.serverHost + '/addCam', {
			'url': data.url,
			'name': data.name
		}, {
			'withCredentials': false
		});
		if (res.status === 201 || res.status === 200) return res.data.id;
		return null;
	} catch (error) {
		console.log(error);
		return null;
	}
};
