import { call, put, takeLatest, all, delay } from 'redux-saga/effects';
import { fetchThemeSuccess, setThemeSuccess } from './actions';

import * as api from '../../../Services/api';

function* _fetchTheme({ userId }) {
	yield delay(1500);
	try {
		const res = yield call(api.getTheme, userId);
		console.log(res);
		yield put(fetchThemeSuccess(res));
	} catch (error) {
		console.log(error);
	}
}

function* _setTheme({ newTheme, userId }) {
	const { Select, Background, Buttons1, Buttons2, Text1, Text2 } = newTheme;
	yield delay(1500);
	try {
		const data = {
			// andré: data é um objeto inteiro com um id,
			Select,
			Background,
			Buttons1,
			Buttons2,
			Text1,
			Text2,
			id: userId,
		};
		const res = yield call(api.editTheme, data);
		if (res.error) {
			// tratar o erro para o usuário
			console.log(res.error);
		}
		yield put(setThemeSuccess(newTheme));
	} catch (error) {
		console.log('theme ->', error);
	}
}

export default all([
	takeLatest('@theme/FETCH_THEME', _fetchTheme),
	takeLatest('@theme/SET_THEME', _setTheme),
]);
