import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBell } from '@fortawesome/free-regular-svg-icons';

const Container = styled.div`
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: Poppins, sans-serif;

	&:hover div:last-child {
		opacity: 1;
		visibility: visible;
		transform: none;
	}

	strong {
		font-size: 24px;
		color: ${({ theme }) => theme.colors.Text1};
		text-transform: uppercase;
		margin-right: 5px;
		transition: opacity 0.2s;
	}

	> span {
		position: absolute;
		display: block;
		text-align: center;
		line-height: 18px;
		width: 16px;
		height: 16px;
		background: #ff4e4e;
		right: -4px;
		top: 0;
		border-radius: 50%;
		z-index: 5;
		font-size: 10px;
		overflow: hidden;
		color: #fff;
	}
`;

const Dropdown = styled.div`
	position: absolute !important;
	top: 100%;
	background: ${({ theme }) => theme.colors.Background};
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
	z-index: 5;
	width: 330px;
	max-height: 60vh;
	overflow-y: auto;
	border: solid 1px #e9e9e9;
	border-radius: 10px;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s;
	transform: translateY(10px);
	right: 0;
	display: flex;
	flex-direction: column-reverse;

	@media (max-width: 768px) {
		right: -50px;
	}
	/* @media (min-width: 1025px) {
		left: 0;
	} */

	> div {
		display: flex;
		align-items: flex-start;
		padding: 16px;
		color: ${({ theme }) => theme.colors.Text1};
		width: 100%;
		border-bottom: solid 1px #e9e9e9;
		& div:last-child {
			border: none;
		}

		.icon {
			min-width: 20px;
			height: auto;
			margin-right: 8px;
			color: ${({ theme }) => theme.colors.Select};
		}

		.error {
			display: block !important;
			width: 200px;

			.titleError {
				font-size: 13px;
				font-weight: 600;
			}
			.msgError {
				margin-top: 8px;
				font-size: 12px;
				line-height: 12px;
				opacity: 0.4 !important;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				max-height: 13px;
			}
		}
		span {
			min-width: 40px;
			font-size: 12px;
			opacity: 0.4;
			text-align: right;
		}
	}
`;

// temporary icons
const VolumeIcon = () => (
	<svg
		xmlns='http://www.w3.org/2000/svg'
		xmlnsXlink='http://www.w3.org/1999/xlink'
		aria-hidden='true'
		role='img'
		preserveAspectRatio='xMidYMid meet'
		viewBox='0 0 16 16'>
		<g fill='currentColor'>
			<path d='M11.536 14.01A8.473 8.473 0 0 0 14.026 8a8.473 8.473 0 0 0-2.49-6.01l-.708.707A7.476 7.476 0 0 1 13.025 8c0 2.071-.84 3.946-2.197 5.303l.708.707z'></path>
			<path d='M10.121 12.596A6.48 6.48 0 0 0 12.025 8a6.48 6.48 0 0 0-1.904-4.596l-.707.707A5.483 5.483 0 0 1 11.025 8a5.483 5.483 0 0 1-1.61 3.89l.706.706z'></path>
			<path d='M8.707 11.182A4.486 4.486 0 0 0 10.025 8a4.486 4.486 0 0 0-1.318-3.182L8 5.525A3.489 3.489 0 0 1 9.025 8A3.49 3.49 0 0 1 8 10.475l.707.707zM6.717 3.55A.5.5 0 0 1 7 4v8a.5.5 0 0 1-.812.39L3.825 10.5H1.5A.5.5 0 0 1 1 10V6a.5.5 0 0 1 .5-.5h2.325l2.363-1.89a.5.5 0 0 1 .529-.06z'></path>
		</g>
	</svg>
);

const MicrophoneIcon = () => (
	<svg
		xmlns='http://www.w3.org/2000/svg'
		xmlnsXlink='http://www.w3.org/1999/xlink'
		aria-hidden='true'
		role='img'
		preserveAspectRatio='xMidYMid meet'
		viewBox='0 0 16 16'>
		<g fill='currentColor'>
			<path d='M3.5 6.5A.5.5 0 0 1 4 7v1a4 4 0 0 0 8 0V7a.5.5 0 0 1 1 0v1a5 5 0 0 1-4.5 4.975V15h3a.5.5 0 0 1 0 1h-7a.5.5 0 0 1 0-1h3v-2.025A5 5 0 0 1 3 8V7a.5.5 0 0 1 .5-.5z'></path>
			<path d='M10 8a2 2 0 1 1-4 0V3a2 2 0 1 1 4 0v5zM8 0a3 3 0 0 0-3 3v5a3 3 0 0 0 6 0V3a3 3 0 0 0-3-3z'></path>
		</g>
	</svg>
);

const CameraIcon = () => (
	<svg
		xmlns='http://www.w3.org/2000/svg'
		xmlnsXlink='http://www.w3.org/1999/xlink'
		aria-hidden='true'
		role='img'
		preserveAspectRatio='xMidYMid meet'
		viewBox='0 0 16 16'>
		<g fill='currentColor'>
			<path
				fillRule='evenodd'
				d='M0 5a2 2 0 0 1 2-2h7.5a2 2 0 0 1 1.983 1.738l3.11-1.382A1 1 0 0 1 16 4.269v7.462a1 1 0 0 1-1.406.913l-3.111-1.382A2 2 0 0 1 9.5 13H2a2 2 0 0 1-2-2V5zm11.5 5.175l3.5 1.556V4.269l-3.5 1.556v4.35zM2 4a1 1 0 0 0-1 1v6a1 1 0 0 0 1 1h7.5a1 1 0 0 0 1-1V5a1 1 0 0 0-1-1H2z'></path>
		</g>
	</svg>
);

// main component
export const Notification = ({
	errors, // from redux
}) => {
	// time now
	const date = new Date();

	// switch icon
	const switchIcon = (i) => {
		switch (i) {
			case 'audio':
				return <VolumeIcon />;

			case 'mic':
				return <MicrophoneIcon />;

			case 'camera':
				return <CameraIcon />;

			default:
				return null;
		}
	};

	// time last
	const getTimeLast = (t) => {
		if (!typeof t.getTime === 'function') return;
		const diffTime = Math.abs(date.getTime() - t.getTime()) / 1000;
		const minutes = Math.floor(diffTime / 60) % 60;
		const hours = Math.floor(diffTime / 3600) % 24;

		if (hours > 1) return `${hours}h`;

		if (minutes > 1) return `${minutes}min`;

		if (minutes <= 1) return 'agora';

		return null;
	};

	// main component
	return (
		<Container>
			{errors.length ? (
				<span>{errors.length > 9 ? '+9' : errors.length}</span>
			) : null}
			<strong>
				<FontAwesomeIcon icon={faBell} />
			</strong>
			{errors.length ? (
				<Dropdown>
					{errors.map((ele, idx) => (
						<div key={idx}>
							<div className='icon'>{switchIcon(ele.type)}</div>
							<div className='error'>
								<div className='titleError'>{ele.titleError}</div>
								<div className='msgError'>{ele.msgError}</div>
							</div>
							<span>{getTimeLast(ele.issued_At)}</span>
						</div>
					))}
				</Dropdown>
			) : null}
		</Container>
	);
};

const mapStateToProps = (state) => ({
	errors: state.errors.listErrors,
});

export default connect(mapStateToProps)(Notification);
//
