import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import Notification from './Notification';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
	faHome,
	faTv,
	faVideo,
	faFolder,
	faPlus,
	faBars,
	faTimes,
} from '@fortawesome/free-solid-svg-icons';

import logo from '../assets/Logo.png';

const Container = styled.div`
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 56px;
	background: ${({ theme }) => theme.colors.Background};
	color: ${({ theme }) => theme.colors.Text1};
	padding: 8px 32px;
	z-index: 1000;
	display: flex;
	justify-content: space-between;
	align-items: center;
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
	> img {
		width: 100px;
		height: auto;
	}
	> div {
		display: flex;
	}
`;

const Menu = styled.div`
	font-size: 24px;
	margin-left: 24px;
`;

const MenuDropdown = styled.div`
	position: absolute;
	top: 0;
	right: 0;
	z-index: 1500;
	width: ${({ opened }) => (opened ? '100vw' : 0)};
	height: 100vh !important;
	background: ${({ theme }) => theme.colors.Background};
	transition: all ease 0.3s;
	div {
		overflow: hidden;
		padding: 34px;
		display: block;
		li,
		span {
			list-style: none;
			padding: 16px;
			font: normal normal 700 16px Poppins !important;
			color: ${({ theme }) => theme.colors.Text1};
			a {
				color: ${({ theme }) => theme.colors.Text1};
				text-decoration: none;
			}
		}
	}
`;

function MobileMenu({ user }) {
	const [isMobile, setIsMobile] = useState(false);
	const [isOpened, setIsOpened] = useState(false);

	useEffect(() => {
		const width = window.screen.width;
		if (width <= 768) {
			setIsMobile(true);
		}
	}, []);

	const toggleMenuDropdown = () => {
		setTimeout(() => setIsOpened(!isOpened), 100);
	};

	if (isMobile)
		return (
			<Container>
				<img src={logo} alt='logo' />
				<div>
					<Notification />
					<Menu onClick={toggleMenuDropdown}>
						<FontAwesomeIcon icon={faBars} />
					</Menu>
					<MenuDropdown opened={isOpened}>
						<div>
							<span onClick={toggleMenuDropdown}>
								<FontAwesomeIcon icon={faTimes} />
							</span>
						</div>
						<div>
							<ul>
								<li>
									<FontAwesomeIcon icon={faHome} />
									<Link to='/home' onClick={toggleMenuDropdown}>
										Home
									</Link>
								</li>
								{user.role === 'admin' ? (
									<li>
										<FontAwesomeIcon icon={faTv} style={{ color: 'inherit' }} />
										<Link to='/management' onClick={toggleMenuDropdown}>
											Gerenciar Usuários
										</Link>
									</li>
								) : null}
								<li>
									<FontAwesomeIcon icon={faVideo} />
									<Link to='/cameras' onClick={toggleMenuDropdown}>
										Câmeras
									</Link>
								</li>
								<li>
									<FontAwesomeIcon icon={faFolder} />
									<Link to='/files' onClick={toggleMenuDropdown}>
										Arquivos
									</Link>
								</li>
								<li>
									<FontAwesomeIcon icon={faPlus} />
									<Link to='/transmission' onClick={toggleMenuDropdown}>
										Nova Transmissão
									</Link>
								</li>
							</ul>
						</div>
					</MenuDropdown>
				</div>
			</Container>
		);

	return null;
}

const mapStateToProps = (state) => ({
	user: state.user.profile,
});

export default connect(mapStateToProps)(MobileMenu);
