/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import VideoJS from '../components/VideoJS';
import io from 'socket.io-client';

import * as api from '../Services/api';
import { StreamDialog } from '../components/StreamDialog';
import { Container, Row, Col, Modal, Spinner } from 'react-bootstrap';
import './Transmission.css';

import { connect } from 'react-redux';

import ListOfSubtitles from '../components/ListOfSubtitles';
import { StyledButton, Form } from '../styles/Styles';
import DropdownCameras from '../components/DropdownCameras';
import {
	streamingStart,
	streamingStop,
} from '../store/modules/streaming/actions';

import PresetsControl from '../components/PresetsControl';
import { connectErrors, addError } from '../store/modules/errors/actions';
import AudioControl from '../components/AudioControl';

const Transmission = ({
	user: User,
	dispatch,
	selectedCamera,
	transmitionStatus,
}) => {
	const [serverHost, setServerHost] = useState(null);

	const logo = api.cfg.API_ROOT + User.avatar;
	const [text, setText] = useState(null);
	const [selCam, setSelCam] = useState({
		name: '',
		id: '',
		owner: '',
	});

	const [startingStream, setStartingStream] = useState(false);
	const [loadingData, setLoadingData] = useState(false);
	const [loadingVideo, setLoadingVideo] = useState(false);
	const [CamsArray, setCamsArray] = useState(null);
	const [videoType, setVideoType] = useState(null);
	const [videoSource, setVideoSource] = useState(null);
	const [googleAccessToken, setGoogleAccessToken] = useState(null);
	const [facebookAccessToken, setFacebookAccessToken] = useState(null);
	const [CamId, setCamId] = useState(null);

	const socket = useRef(null);

	// legendas
	const [isLoadingSubtitles, setIsLoadingSubtitles] = useState(false);
	const [showModalSub, setShowModalSub] = useState(false);
	const [subtitles, setSubtitles] = useState([]);
	const [newTitle, setNewTitle] = useState('');
	const [newSubtitle, setNewSubtitle] = useState('');

	// audio
	const [microphone, setMicrophone] = useState(0.5);
	const [audio, setAudio] = useState(0.5);

	useEffect(() => {
		loadSubtitles();
	}, []);

	const loadSubtitles = async () => {
		setIsLoadingSubtitles(true);
		const res = await api.getSubtitles(User.id);
		console.log(res);
		setSubtitles(res);
		setIsLoadingSubtitles(false);
	};

	const addSubtitle = async (newTitle, newSubtitle) => {
		setShowModalSub(false);
		const { id } = User;
		await api.addNewSubtitle({ userId: id, newTitle, newSubtitle });
		await loadSubtitles();
	};

	const getCameras = async () => {
		const response = await api.getCams(User.id);
		if (!response.error) {
			console.log('CamsArray->', response);
			setCamsArray(response);
			setSelCam(response[0]);
		} else {
			console.log(response.error.message);
		}
	};

	const responsefacebook = (response) => {
		console.log(response);
		if (response.accessToken) {
			console.log('FB_TOKEN->', response.accessToken);
			setFacebookAccessToken(response.accessToken);
			console.log('FB_RESPONSE->', response);
		} else {
			console.log('Erro ao obter token');
		}
	};

	const getServer = async () => {
		const response = await api.getServer();
		if (response) {
			if (!response.error) {
				if (response) {
					console.log('Server get response in Transmission->', response);

					const url = `https://${response[0].url}`;
					setServerHost(url);
					socket.current = io(url);
				}
			} else {
				console.log(response.error.message);
			}
		}
	};

	useEffect(() => {
		getServer();
		getCameras();
	}, []);

	async function addCam() {
		if (serverHost) {
			setLoadingData(true);

			const id = await api.addCam({
				serverHost: serverHost,
				name: selectedCamera.name,
				url: selectedCamera.url,
			});
			console.log('id---->', id);
			setCamId(id);
			setLoadingData(false);
			setLoadingVideo(true);
		}
	}

	useEffect(() => {
		// setCamId(null)
		// console.log("useEffect1")
		// addCam();
	}, []);

	useEffect(() => {
		if (selectedCamera) {
			setCamId(null);
			console.log('useEffect2');
			addCam();
		}
	}, [selectedCamera, serverHost]);

	let videoJsOptions = {
		autoplay: 'play',
		controls: true,
		responsive: true,
		fluid: true,
	};

	const startLiveStream = async (streamData) => {
		const data = {
			streamData: streamData,
			Gtoken: googleAccessToken,
			FBtoken: facebookAccessToken,
			logo: logo,
			
		};

		let stream = null;
		try {
			stream = await window.navigator.mediaDevices.getUserMedia({
				audio: true,
			});
			socket.current.emit('create-connection', data);
		} catch (error) {
			console.log('entrou na condição False!! -->', error);
			dispatch(
				addError({
					type: 'mic',
					titleError: 'Falha do Microfone.',
					msgError: 'o Uso do microfone não foi autorizado.',
				})
			);
		}

		// alteração para receber os ids do Facebook e do YouTube
		// liveIds = {
		//   facebookLiveId: '',
		//   youtubeLiveId: ''
		// }
		socket.current.on('liveVideo', (liveIds) => {
			if (liveIds) {
				dispatch(streamingStart({ data, liveIds }));
				dispatch(connectErrors(socket.current));
			}
		});

		socket.current.on('connection-created', async (remotePeerConnection) => {
			const { id } = remotePeerConnection;

			const localPeerConnection = new RTCPeerConnection({
				sdpSemantics: 'unified-plan',
				iceServers: [
					{ urls: ['stun:34.121.194.69:3478', 'stun:stun.l.google.com:19302'] },
					{
						urls: ['turn:34.121.194.69:3478'],
						username: 'rco',
						credential: 'TXzb8aYA',
					},
				],
			});

			localPeerConnection.close = function () {
				socket.current.emit('connection-delete', { id });

				// stop streaming
				dispatch(streamingStop());

				return RTCPeerConnection.prototype.close.apply(this, arguments);
			};

			await localPeerConnection.setRemoteDescription(
				remotePeerConnection.localDescription
			);

			await getUserMedia(localPeerConnection, stream);

			const originalAnswer = await localPeerConnection.createAnswer();
			const updatedAnswer = new RTCSessionDescription({
				type: 'answer',
				sdp: originalAnswer.sdp,
			});
			await localPeerConnection.setLocalDescription(updatedAnswer);

			socket.current.emit('connection-remote-description', {
				id,
				body: localPeerConnection.localDescription,
			});

			// close stream dialog
			setStartingStream(false);
		});
	};

	const getUserMedia = async (localPeerConnection, stream) => {
		try {
			//const stream = await window.navigator.mediaDevices.getUserMedia({
			//audio: true
			//});
			stream
				.getTracks()
				.forEach((track) => localPeerConnection.addTrack(track, stream));

			const { close } = localPeerConnection;
			localPeerConnection.close = function () {
				stream.getTracks().forEach((track) => track.stop());
				return close.apply(this, arguments);
			};
		} catch (error) {
			console.error(error);
		}
	};

	return (
		<div style={{ paddingBottom: 50 }} className='fade-in'>
			<Row className='title-row'>
				<Col xl={6} className='title-col'>
					<h1 className='cameras-title'>
						<span>Transmissão</span>
						<span>{' > '}</span>
						{selectedCamera ? (
							<span>{selectedCamera.name}</span>
						) : (
							<span className='disabled'>Selecione uma câmera</span>
						)}
					</h1>
				</Col>
				<Col xl={6} className='button-col'>
					<DropdownCameras
						disabled={transmitionStatus}
						items={CamsArray || []}
						socket={socket.current}
					/>

					{/* set transmittion */}
					{transmitionStatus ? (
						<StyledButton
							variant='danger'
							onClick={(e) => {
								dispatch(streamingStop());
								socket.current.emit('finish-transmission');
							}}>
							Encerrar transmissão
						</StyledButton>
					) : (
						<>
						<StyledButton
							disabled={!selectedCamera}
							variant='success'
							onClick={() => {
								setStartingStream(true);

								if (socket.current) {
									socket.current.emit('get-video', {});
								}
							}}>
							Iniciar transmissão
						</StyledButton>
						{/* <button
						onClick={async ()=>{
							let res = null
							res = await api.getPresets(1)
							console.log("GetPresets ====> ", res)
						}}>
							Get Presets
							</button> */}
							</>
					)}

					{/* BOTOES SO DE TESTE */}

					{/* <button
						onClick={() => {
							if (socket.current) {
								socket.current.emit('change-google', {});
							}
						}}>
						adicionar legenda
					</button>
					<button
						onClick={() => {
							if (socket.current) {
								socket.current.emit('change-cam', {
									streamData: {
										mainCam:
											'rtsp://demo:demo@ipvmdemo.dyndns.org:5541/onvif-media/media.amp?',
									},
									FBtoken: facebookAccessToken,
									logo: logo,
								});
							}

							sendMessage();
						}}>
						Alterar Camera
					</button> */}
				</Col>
			</Row>
			<Row>
				{/* andré: grid view */}
				<Container fluid className='container-gridview'>
					<div className='container-vid'>
						<div>
							{CamId ? (
								<video
									onLoadedData={() => {
										setLoadingVideo(false);
									}}
									autoPlay='autoplay'
									muted
									controls>
									<source
										src={`${serverHost}/stream/${CamId}`}
										type='video/mp4'
									/>
								</video>
							) : null}
							{loadingData && (
								<div
									style={{
										width: '100%',
										height: '100%',
										display: 'flex',
										justifyContent: 'center',
										alignItems: 'center',
										position: 'absolute',
									}}>
									<Spinner animation='border' />
								</div>
							)}
							{loadingVideo && (
								<div
									style={{
										width: '100%',
										height: '100%',
										display: 'flex',
										justifyContent: 'center',
										alignItems: 'center',
										position: 'absolute',
									}}>
									<Spinner animation='border' />
								</div>
							)}

							{/* <VideoJS
								options={{
									...videoJsOptions,
									sources: [
										{
											src: videoSource,
											type: videoType,
										},
									],
								}}
							/> */}
						</div>
					</div>
					{/* lista de legendas */}
					<div className='container-legendas'>
						<div className='title'>Legendas</div>
						<div className='normalize-height'>
							<ListOfSubtitles
								items={subtitles || []}
								socket={socket.current}
								isLoading={isLoadingSubtitles}
							/>
						</div>
					</div>
				</Container>
			</Row>
			<div className='actions-transmition '>
				<div>
					<StyledButton variant='accent' onClick={(e) => setShowModalSub(true)}>
						Adicionar legenda
					</StyledButton>
				</div>
			</div>
			{/* controle de áudio */}
			{/* fora no escopo do projeto */}
			{/* <Row>
				<AudioControl
					// value of microphone between 0 and 1
					microphone={(mic) => {
						console.log('mic', mic);
						setMicrophone(mic);
					}}
					// value of audio between 0 and 1
					audio={(audio) => {
						console.log('audio', audio);
						setAudio(audio);
					}}
				/>
			</Row> */}
			{/* controle de presets da câmera */}
			<Row>
				<PresetsControl />
			</Row>
			<StreamDialog
				show={startingStream}
				responsefacebook={responsefacebook}
				setGoogleAccessToken={setGoogleAccessToken}
				cameras={CamsArray || []}
				onHide={() => {
					setStartingStream(false);
				}}
				startTransmition={(data) => {
					startLiveStream(data);
				}}
			/>
			{/* modal add subtitle */}
			<Modal
				centered
				show={showModalSub}
				onHide={(e) => setShowModalSub(false)}>
				<Modal.Body>
					<Form>
						<div className='closebox'>
							<FontAwesomeIcon
								icon={faTimes}
								onClick={(e) => setShowModalSub(false)}
							/>
						</div>
						<div className='header'>Adicionar legenda</div>
						<div>
							<span>Título</span>
							<input
								type='text'
								placeholder='Título'
								onChange={(e) => setNewTitle(e.target.value)}
							/>
						</div>
						<div>
							<span>Subtítulo</span>
							<input
								input
								type='text'
								placeholder='Subtítulo'
								onChange={(e) => setNewSubtitle(e.target.value)}
							/>
						</div>
						<div>
							<button
								disabled={!newTitle || !newSubtitle}
								onClick={(e) => addSubtitle(newTitle, newSubtitle)}>
								<span>Adicionar</span>
							</button>
						</div>
					</Form>
				</Modal.Body>
			</Modal>
		</div>
	);
};

const mapStateToProps = (state) => ({
	user: state.user.profile,
	theme: state.theme,
	selectedCamera: state.camera.selected,
	transmitionStatus: state.streaming.on,
});

export default connect(mapStateToProps)(Transmission);
//
