import styled, { css } from 'styled-components';
import { Link } from 'react-router-dom';

// andré: tranforma o SelectButton em Link nativo
export const SelectedButtonLink = styled(Link)`
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	text-decoration: none !important;
	margin-bottom: 8px;
	border-radius: 5px;
	height: 54px;

	background-color: ${({ type, selected, theme }) => {
		switch (type) {
			case 'secondary':
				return theme.colors.Text1;

			default:
				return selected ? theme.colors.Select : theme.colors.Background;
		}
	}};

	color: ${({ type, selected, theme }) => {
		switch (type) {
			case 'secondary':
				return theme.colors.Text2;

			default:
				return selected ? theme.colors.Text2 : theme.colors.Text1;
		}
	}} !important;

	border: none;
	font: normal normal 600 15px/25px Poppins;
	letter-spacing: 0px;
	padding-left: 20px;
	transition: all ease 0.2s;
	svg {
		color: inherit;
	}
	&:hover {
		filter: brightness(120%) saturate(120%);
	}
`;
