export const Form = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: stretch;
	align-items: center;
	font-family: Poppins, sans-serif;
	color: ${({ theme }) => theme.colors.Text1};

	.closebox {
		width: 100%;
		display: flex;
		align-items: flex-end;
		padding: 8px 16px;
		svg {
			cursor: pointer;
			font-size: 24px;
			fill: ${({ theme }) => theme.colors.Text1};
		}
	}

	.header {
		padding: 20px 0 40px 0;
		align-items: center;
		font-size: 18px;
		font-weight: 600;
	}

	div {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		padding-bottom: 24px;
		padding: 0 40px 24px 40px;
		@media (max-width: 768px) {
			padding: 0 15px 12px 15px;
		}
		span {
			font-size: 14px;
			font-weight: 600;
		}
	}

	input {
		outline: none;
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		width: 100%;
		height: 54px;
		padding-left: 20px;
		padding-right: 20px;
		background: #ffffff 0% 0% no-repeat padding-box;
		border: 2px solid #e9e9e9;
		border-radius: 10px;
		opacity: 1;
		text-align: left;
		font: normal normal normal 16px/25px Poppins;
		letter-spacing: 0px;
		/* color: #cccccc; */
		&::placeholder {
			color: #cccccc;
		}
	}

	button {
		display: flex;
		align-items: center;
		background: #e9e9e9;
		width: 100%;
		min-height: 50px;
		margin: 30px 0;
		border-radius: 10px;
		justify-content: center;
		font: normal normal 500 16px/25px Poppins;
		letter-spacing: 0px;
		color: ${({ theme }) => theme.colors.Text1};
		border: none;
		outline: none;
		height: 40px;
		svg {
			margin-right: 6px;
		}
		&:disabled {
			filter: opacity(0.5);
		}
	}
`;
