import React from 'react';
import './MyButton.css';
import { Link } from 'react-router-dom';
// andré: conecta o ButtonLogout a store
import { connect } from 'react-redux';
import { fetchLogoutUser } from '../store/modules/user/actions';

const STYLES = [
	'btn--primary',
	'btn--outline',
	'btn--test',
	'btn--footer',
	'btn--user',
];

const SIZES = ['btn--medium', 'btn--large', 'btn--footer'];

export const MyButton = ({
	children,
	type,
	onClick,
	buttonStyle,
	buttonSize,
	Route,
}) => {
	const checkButtonStyle = STYLES.includes(buttonStyle)
		? buttonStyle
		: STYLES[0];

	const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

	return (
		<Link to={Route} className='btn-mobile'>
			<button
				className={`btn ${checkButtonStyle} ${checkButtonSize}`}
				onClick={onClick}
				type={type}>
				{children}
			</button>
		</Link>
	);
};

// andré: logout user
const ButtonLogout = ({
	children,
	type,
	buttonStyle,
	buttonSize,
	dispatch,
}) => {
	const checkButtonStyle = STYLES.includes(buttonStyle)
		? buttonStyle
		: STYLES[0];

	const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

	const logoutUser = () => {
		dispatch(fetchLogoutUser());
	};

	return (
		<button
			className={`btn ${checkButtonStyle} ${checkButtonSize}`}
			onClick={logoutUser}
			type={type}>
			{children}
		</button>
	);
};
export default connect(null)(ButtonLogout);

// default button (without link)
export const Button = ({
	children,
	type,
	buttonStyle,
	buttonSize,
	onClick,
}) => {
	const checkButtonStyle = STYLES.includes(buttonStyle)
		? buttonStyle
		: STYLES[0];

	const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

	return (
		<button
			className={`btn ${checkButtonStyle} ${checkButtonSize}`}
			onClick={onClick}
			type={type}>
			{children}
		</button>
	);
};
