const mapStateToProps = (state) => ({
	user: state.user.profile,
	theme: state.theme,
	tokenfb: state.streaming.FBtoken,
	viewsfb: state.streaming.FBViews,
	tokengl: state.streaming.Gtoken,
	viewsgl: state.streaming.GlViews,
});

export default connect(mapStateToProps)(Home);
