import React, { Component } from "react";
import ReactDOM from "react-dom";
import ReactCardCarousel from "react-card-carousel";
import "./Carousel.css";
import img1 from "../assets/img1.png";
import img2 from "../assets/img2.png";
import img3 from "../assets/img3.png";

export class MyCarousel extends Component {
  render() {
    return (
      <div className="carouselcont">
        <ReactCardCarousel autoplay={true} autoplay_speed={3000}>
          <div className="carousel">
            <img className="img" src={img1} alt="Primeiro Card" />
          </div>
          <div className="carousel">
            <img className="img" src={img2} alt="Segundo Card" />
          </div>
          <div className="carousel">
            <img className="img" src={img3} alt="Terceiro Card" />
          </div>
        </ReactCardCarousel>
      </div>
    );
  }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<MyCarousel />, rootElement);
