//   else return Styles[color];
// };

export const Icon = (props) => {
  switch (props.type) {
    case "home":
      return (
        <FontAwesomeIcon
          icon={faHome}
          style={{ color: props.color }}
        ></FontAwesomeIcon>
      );
    case "tv":
      return (
        <FontAwesomeIcon
          icon={faTv}
          style={{ color: props.color }}
        ></FontAwesomeIcon>
      );
    case "camera":
      return (
        <FontAwesomeIcon
          icon={faVideo}
          style={{ color: props.color }}
        ></FontAwesomeIcon>
      );
    case "folder":
      return (
        <FontAwesomeIcon
          icon={faFolder}
          style={{ color: props.color }}
        ></FontAwesomeIcon>
      );
    case "plus":
      return (
        <FontAwesomeIcon
          icon={faPlus}
          style={{ color: props.color }}
        ></FontAwesomeIcon>
      );
    default:
      return null;
  }
};
