import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectSubtitle } from '../store/modules/subtitle/actions';
import styled from 'styled-components';
import { Spinner } from 'react-bootstrap';

const Container = styled.div`
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	/* height: 100%; */
	min-height: 100%;
`;

const Subtitles = styled.div`
	display: flex;
	flex-direction: column;
	cursor: pointer;
	border-radius: 5px;
	background: ${({ theme }) => theme.colors.Background};
	transition: transform ease 0.2s;
	margin-bottom: 8px;
	padding: 8px;
	border: ${({ selected, theme }) =>
		selected
			? `solid 2px ${theme.colors.Select}`
			: `solid 2px ${theme.colors.Background}`} !important;
	font-family: 'Poppins', sans-serif;
	color: ${({ theme }) => theme.colors.Text1};
	span {
		white-space: nowrap;
		overflow-x: hidden;
		text-overflow: ellipsis;
	}
	> span.title-subs {
		font-size: 16px;
	}
	> span.desc-subs {
		font-size: 12px;
		opacity: 0.4;
	}
`;

// new component
/**
 * List of subtitles
 * @param {{
 * items: [],
 * socket: Socket,
 * subtitle: string,
 * colorBackground: string,
 * dispatch: Function
 * }} param
 * @returns
 */
const ListOfSubtitles = ({
	items, // items gerado na página Transmition
	socket, // socket criado na página Transmition
	isLoading, // from transmition page
	subtitle, // from redux store
	colorBackground, // from redux store
	dispatch, // from redux store
}) => {
	// select
	const toggleSelect = (e, item) => {
		if (item.id === subtitle.id) return;
		const data = {
			...item,
			colorBackground,
		};
		dispatch(selectSubtitle(socket, data));
	};

	// main component

	if (isLoading)
		return (
			<Container>
				<Spinner size='sm' animation='border' />
			</Container>
		);

	return (
		<Container>
			{items.length
				? items.map((ele) => (
						<Subtitles
							key={ele.id}
							selected={subtitle.id === ele.id}
							onClick={(e) => toggleSelect(e, ele)}>
							<span className='title-subs'>{ele.title}</span>
							<span className='desc-subs'>{ele.subtitle}</span>
						</Subtitles>
				  ))
				: null}
		</Container>
	);

	// render
	// if (isLoading) return <Spinner size='sm' animation='border' />;
	// if (!items.length) return null;
	// return items.map((ele) => (
	// 	<Subtitles
	// 		key={ele.id}
	// 		selected={subtitle.id === ele.id}
	// 		onClick={(e) => toggleSelect(e, ele)}>
	// 		<span className='title-subs'>{ele.title}</span>
	// 		<span className='desc-subs'>{ele.subtitle}</span>
	// 	</Subtitles>
	// ));
};

const mapStateToProps = (state) => ({
	subtitle: state.subtitle.selected,
	colorBackground: state.theme.colors.Background,
});

ListOfSubtitles.protoType = {
	items: PropTypes.array, // items gerado na página Transmition
	socket: PropTypes.object, // socket criado na página Transmition
	subtitle: PropTypes.string, // from redux store
	colorBackground: PropTypes.string, // from redux store
	dispatch: PropTypes.func, // from redux store
};

export default connect(mapStateToProps)(ListOfSubtitles);
