import React, { useState, useRef } from 'react';
import './AddUser.css';
import { Modal, Col, Row, Spinner } from 'react-bootstrap';
import InputMask from 'react-input-mask';
import { FileUploader } from '../components/FileUploader';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import * as api from '../Services/api';

const resetForm = () => {
	document.getElementById('add-user-form').reset();
};

function validatePassword() {
	var Pass = document.getElementById('password-field'),
		confirm = document.getElementById('password-confirmation');
	if (Pass.value !== confirm.value) {
		confirm.setCustomValidity('Confirmação incorreta.');
		return false;
	} else {
		confirm.setCustomValidity('');
		return true;
	}
}

export const AddUser = (props) => {
	const emailField = useRef(null);
	const confpass = useRef(null);
	const [loading, setLoading] = useState(false);
	const [doc, setDocument] = useState('');
	const [address, setAddress] = useState('');
	const [city, setCity] = useState('');
	const [uf, setUf] = useState('');
	const [selectedFile, setSelectedFile] = useState(null);
	const onFileSelectSuccess = (file) => {
		console.log('File Selected!');
		console.log(file);
		setSelectedFile(file);
	};

	const onFileSelectError = (error) => {
		console.log('error-> ', error);
	};

	const handleSubmit = async (formdata) => {
		// console.log("entrou handleSubmit");
		const data = formdata;
		if (selectedFile) {
			data.append('file', selectedFile);
			data.append('filename', selectedFile.name);
			data.append('path', 'images');
		}

		// const data = {
		//   filename: selectedFile.name,
		//   path: "images",
		//   user: newUser,
		//   file: selectedFile,
		// };
		const response = await api.createUser(data);
		if (response) {
			setLoading(false);
			props.setAddUserModal(false);

			if (!response.error) {
				console.log('response in Management->', response);
				//mensagem de sucesso
			} else {
				setLoading(false);
				console.log(response.error.message);
				props.setAddUserModal(false);
				//mensagem de erro
			}
			setSelectedFile(null);
		}
	};

	const handleAutoCompleteCep = async (value) => {
		const cep = value.replace(/[^0-9]/g, '');

		if (cep.length !== 8) {
			return;
		}

		const response = await api.getViaCep(cep);

		if (response.erro) {
			return;
		}

		setAddress(response.logradouro);
		setCity(response.localidade);
		setUf(response.uf);
	};
	const validateEmail = async (email) => {
		const response = await api.checkUser({ email: email });
		if (!response.error) {
			return true;
		} else {
			return false;
		}
	};

	const closeModal = () => {
		props.setAddUserModal(false);
		resetForm();
		setDocument('');
		setAddress('');
		setUf('');
		setCity('');
		// props.onHide();
	};

	return (
		<Modal
			// dialogClassName='modal-80w'
			{...props}
			size='lg'
			scrollable
			aria-labelledby='contained-modal-title-vcenter'
			centered>
			<Modal.Body>
				<div className='closebox-modal'>
					<FontAwesomeIcon icon={faTimes} onClick={closeModal} />
				</div>
				<form
					className='addUserForm'
					autoComplete='off'
					id='add-user-form'
					onSubmit={async (event) => {
						console.log('submit!!');
						event.preventDefault();
						setLoading(true);
						const elements = event.target.elements;
						const formdata = new FormData();
						formdata.append('name', elements.name.value);
						formdata.append('email', elements.email.value);
						formdata.append('document', elements.document.value);
						formdata.append('phone', elements.tel.value);
						formdata.append('address', elements.address.value);
						formdata.append('st_number', elements.stNum.value);
						formdata.append('city', elements.city.value);
						formdata.append('uf', elements.state.value);
						formdata.append('cep', elements.cep.value);
						formdata.append('password', elements.password.value);
						formdata.append(
							'password_confirmation',
							elements.password_confirm.value
						);
						formdata.append('inicio', elements.inicio.value);
						formdata.append('vencimento', elements.vencimento.value);

						// const newUser = {
						//   name: elements.name.value,
						//   email: elements.email.value,
						//   document: elements.document.value,
						//   phone: elements.tel.value,
						//   address: elements.address.value,
						//   st_number: elements.stNum.value,
						//   city: elements.city.value,
						//   uf: elements.state.value,
						//   cep: elements.cep.value,
						//   password: elements.password.value,
						//   password_confirmation: elements.password_confirm.value,
						//   inicio: elements.inicio.value,
						//   vencimento: elements.vencimento.value,
						// };
						console.log(formdata);

						const response = await validateEmail(elements.email.value);
						console.log('result->', response);
						if (response === true) {
							console.log('validity Checked email');
							emailField.current.setCustomValidity('');
							if (validatePassword() === true) {
								console.log('validity Checked password');
								handleSubmit(formdata);
								resetForm();
								setDocument('');
								setAddress('');
								setUf('');
								setCity('');
								setLoading(false);
							} else setLoading(false);
						} else if (response === false) {
							setLoading(false);
							emailField.current.setCustomValidity(
								'Este e-mail já esta sendo utilizado.'
							);
						}
					}}>
					<Row>
						<Col xl={12} className='col3'>
							<h1 className='title-modal header-modal'>
								Cadastrar novo usuário
							</h1>
						</Col>
						<Col className='col1' xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Nome
								</label>
								<input
									type='text'
									name='name'
									className='inputs-addUser'
									placeholder='Nome'
									required></input>
							</div>
						</Col>
						<Col className='col1' xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									E-mail
								</label>
								<input
									id='email-field'
									type='email'
									name='email'
									required
									className='inputs-addUser'
									placeholder='E-mail'
									ref={emailField}
									onChange={() =>
										emailField.current.setCustomValidity('')
									}></input>
							</div>
						</Col>
						<Col className='col1' xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='document'>
									CPF/CNPJ
								</label>
								<InputMask
									mask={
										doc.length < 15 ? '999.999.999-999' : '99.999.999/9999-99'
									}
									maskChar=''
									name='document'
									value={doc}
									onChange={(e) => {
										setDocument(e.target.value);
										//console.log(doc.replace(/\D/g, ""));
									}}
									required
									className='inputs-addUser'
									placeholder='CPF/CNPJ'></InputMask>
							</div>
						</Col>
						<Col className='col1' xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Telefone
								</label>
								<InputMask
									mask='(99)99999-9999'
									name='tel'
									type='string'
									required
									className='inputs-addUser'
									placeholder='Telefone'></InputMask>
							</div>
						</Col>
						<Col className='col1' xl={4}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Endereço
								</label>
								<input
									type='text'
									name='address'
									required
									className='inputs-endereco'
									placeholder='Endereço'
									value={address}
									onChange={(event) => setAddress(event.target.value)}></input>
							</div>
						</Col>

						<Col className='col2' xl={2}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									N°
								</label>
								<input
									type='text'
									name='stNum'
									className='inputs-numero'
									placeholder='N°'
									required></input>
							</div>
						</Col>

						<Col xl={6} className='col1'>
							<div className='grouping'>
								<label className='labels-contrato'>Foto de Perfil</label>
								<FileUploader
									cName={'inputs-foto'}
									onFileSelectSuccess={onFileSelectSuccess}
									onFileSelectError={onFileSelectError}
									sizeLimit={10485760} //Tamanho limite de 10MB
									text={'Adicionar foto'}
									variant='text-button'
								/>
								{/* <Button variant="light" className="inputs-foto">
                  Adicionar foto
                  <i class="fas fa-plus fplus"></i>
                </Button> */}
							</div>
						</Col>
						<Col xl={6} className='col1'>
							<div className='grouping'>
								<label className='labels-contrato'>Cidade</label>
								<input
									type='text'
									name='city'
									className='inputs-addUser'
									placeholder='Bairro/Cidade'
									value={city}
									onChange={(event) => setCity(event.target.value)}
									required></input>
							</div>
						</Col>
						<Col className='col1' xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Senha
								</label>
								<input
									id='password-field'
									type='password'
									name='password'
									className='inputs-addUser'
									placeholder='Senha'
									required
									onChange={() => validatePassword()}></input>
							</div>
						</Col>

						<Col xl={4} className='col1'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Estado
								</label>
								<input
									type='text'
									name='state'
									className='inputs-endereco'
									placeholder='Estado'
									value={uf}
									onChange={(event) => setUf(event.target.value)}
									required></input>
							</div>
						</Col>
						<Col xl={2}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									CEP
								</label>
								<InputMask
									mask='99999-999'
									type='text'
									name='cep'
									className='inputs-numero'
									placeholder='CEP'
									onBlur={(event) => handleAutoCompleteCep(event.target.value)}
									required></InputMask>
							</div>
						</Col>
						<Col xl={6} className='col1'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Confirmar Senha
								</label>

								<input
									type='password'
									id='password-confirmation'
									name='password_confirm'
									className='inputs-addUser'
									placeholder='Senha'
									required
									ref={confpass}
									onChange={() => {
										confpass.current.setCustomValidity('');
										validatePassword();
									}}></input>
							</div>
						</Col>
						<Col className='col1' xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Início do Contrato
								</label>
								<InputMask
									className='inputs-addUser'
									mask='99/99/9999'
									name='inicio'
									required
									placeholder='DD/MM/AAAA'></InputMask>
							</div>
						</Col>
						<Col className='col1' xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Fim do Contrato
								</label>
								<InputMask
									className='inputs-addUser'
									mask='99/99/9999'
									name='vencimento'
									required
									placeholder='DD/MM/AAAA'></InputMask>
							</div>
						</Col>

						<Col className='col3'>
							{loading ? (
								<Spinner animation='border' size='sm' />
							) : (
								<button className='login-button' type='submit'>
									Cadastrar
								</button>
							)}
						</Col>
					</Row>
				</form>
			</Modal.Body>
		</Modal>
	);
};
