import { call, put, takeLatest, all, delay } from 'redux-saga/effects';
import { fetchLoginUserSuccess, fetchLoginUserError } from './actions';

/*
  como há uma 2a requisição para obter o tema
  é necessária uma interação com a action do theme
*/
import { fetchTheme } from '../theme/actions';

import * as api from '../../../Services/api';

function* _fetchLoginUser(userData) {
	const { email, password } = userData.data;
	yield delay(1500); // simulação assíncrona
	try {
		const res = yield call(api.login, { email, password });
		if (res.error) {
			return yield put(fetchLoginUserError(res.error.message));
		}
		yield put(fetchTheme(res.user.id));
		yield put(fetchLoginUserSuccess(res));
	} catch (error) {
		yield put(fetchLoginUserError('Erro inesperado.'));
	}
}

export default all([takeLatest('@user/FETCH_LOGIN_USER', _fetchLoginUser)]);
