const INITIAL_STATE = {
	selected: {
		id: null,
		name: '',
		owner: '',
		url: '',
		update_at: '',
	},
};

const camera = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case '@camera/SELECT_CAMERA':
			return {
				...state,
				selected: action.camera,
			};

		default:
			return state;
	}
};

export default camera;
