/* eslint-disable eqeqeq */
import React, { useState, useRef, useEffect, useContext } from 'react';
import { Modal } from 'react-bootstrap';
import { ThemeContext } from 'styled-components';
import FacebookLogin from 'react-facebook-login';
import { GLogin } from './GoogleLogin';
import './MyCenteredModal.css';
import { Spinner, Container, Row, Col, Alert } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { ThemeProvider } from 'styled-components';

// style facebook button must be made with css
import '../styles/facebook-button.css';

// styled
import { StyledButton } from '../styles/Styles';
import { Dropdown } from 'react-bootstrap';
const { Toggle, Menu, Item } = Dropdown;

export const StreamDialog = (props) => {
	const theme = useContext(ThemeContext);
	const [title, setTitle] = useState('Iniciando Stream');
	const [googlePermission, setGooglePermission] = useState(true);

	const Ref1 = useRef();
	const Ref2 = useRef();

	// settings transmition
	const [titleTransmition, setTitleTransmition] = useState('');
	const [subtitleTransmition, setSubtitleTransmition] = useState('');
	const [primaryCam, setPrimaryCam] = useState({
		id: null,
		name: '',
		url: '',
	});
	const [secondaryCam, setSecondaryCam] = useState({
		id: null,
		name: '',
		url: '',
	});
	const [variantAlert, setVariantAlert] = useState('');
	const [msgAlert, setMsgAlert] = useState('');
	const [showAlert, setShowAlert] = useState(false);

	const { cameras } = props;

	const selectPrimaryCamera = (e) => {
		const ex = Number(e);
		const idx = cameras.findIndex((i) => i.id === ex);
		setPrimaryCam({
			id: cameras[idx].id,
			name: cameras[idx].name,
			url: cameras[idx].url,
		});
	};

	const selectSecondaryCamera = (e) => {
		const ex = Number(e);
		const idx = cameras.findIndex((i) => i.id === ex);
		setSecondaryCam({
			id: cameras[idx].id,
			name: cameras[idx].name,
			url: cameras[idx].url,
		});
	};

	const startTransmission = () => {
		if (!verify()) {
			return dispatchAlert(
				true,
				'Erro: Informe os dados para iniciar uma nova transmissão.'
			);
		}
		const data = {
			title: titleTransmition,
			subtitle: subtitleTransmition,
			primaryCamera: primaryCam.url,
			secondaryCamera: secondaryCam.url,
		};
		console.log(data);
		props.startTransmition(data);
		props.onHide();
	};

	const verify = () => {
		if (!titleTransmition) return false;
		if (!subtitleTransmition) return false;
		if (!primaryCam.url) return false;
		//if (!secondaryCam.url) return false;
		// tokens obrigatórios 06/10/21
		// ???
		return true;
	};

	const closeAlert = () => {
		setShowAlert(false);
	};

	const dispatchAlert = (error, message) => {
		setVariantAlert(error ? 'danger' : 'success');
		setMsgAlert(message);
		setShowAlert(true);
	};

	return (
		<ThemeProvider theme={theme}>
			<Modal
				scrollable
				// dialogClassName='modal-40w'
				{...props}
				// size='lg'
				aria-labelledby='contained-modal-title-vcenter'
				centered>
				<Modal.Body>
					<div className='closebox-modal'>
						<FontAwesomeIcon icon={faTimes} onClick={(e) => props.onHide()} />
					</div>
					<Container>
						<div className='addUserForm' autocomplete='off'>
							<Row>
								<Col xl={12} className='header-modal'>
									<h1 className='title-contrato'>{title}</h1>
									{/* {loading ? <Spinner animation="border" /> : null} */}
								</Col>
								<Col className='col3' xl={12}>
									<div className='grouping'>
										<label className='labels-contrato' htmlfor='nome'>
											Título da Transmissão
										</label>
										<input
											type='text'
											name='title'
											autoComplete='false'
											className='inputs-addUser'
											placeholder='título'
											onChange={(e) => setTitleTransmition(e.target.value)}
											value={titleTransmition}
											required></input>
									</div>
								</Col>
								<Col className='col3' xl={12}>
									<div className='grouping'>
										<label className='labels-contrato' htmlfor='nome'>
											Descrição
										</label>
										<input
											type='text'
											name='description'
											autoComplete='false'
											className='inputs-addUser'
											placeholder='descrição'
											onChange={(e) => setSubtitleTransmition(e.target.value)}
											value={subtitleTransmition}
											required></input>
									</div>
								</Col>
								<Col xl={12} className='col3'>
									<div className='grouping'>
										<label
											style={{ textAlign: 'center' }}
											className='labels-contrato'
											for='mainCameras'>
											Selecione câmera principal
										</label>
										<Dropdown
											name='mainCameras'
											onSelect={(e) => selectPrimaryCamera(e)}>
											<Toggle
												as={StyledButton}
												style={{ width: '100%', marginRight: 0 }}>
												{primaryCam.name || 'Câmera principal'}
											</Toggle>
											<Menu>
												{cameras.map((ele) => (
													<Item
														key={ele.id}
														eventKey={ele.id}
														value={ele.url}
														active={ele.id === primaryCam.id}>
														{ele.name}
													</Item>
												))}
											</Menu>
										</Dropdown>
									</div>
								</Col>
								{/* <Col xl={12} className='col3'>
									<div className='grouping'>
										<label
											style={{ textAlign: 'center' }}
											className='labels-contrato'
											for='secCameras'>
											Selecione a câmera secundária
										</label>
										<Dropdown
											name='secCameras'
											onSelect={(e) => selectSecondaryCamera(e)}>
											<Toggle
												as={StyledButton}
												style={{ width: '100%', marginRight: 0 }}>
												{secondaryCam.name || 'Câmera secundária'}
											</Toggle>
											<Menu>
												{cameras.map((ele) => (
													<Item
														key={ele.id}
														eventKey={ele.id}
														value={ele.url}
														active={ele.id === secondaryCam.id}>
														{ele.name}
													</Item>
												))}
											</Menu>
										</Dropdown>
									</div>
								</Col> */}
								<Col className='col3' xl={12}>
									<GLogin
										setToken={props.setGoogleAccessToken}
										setGooglePermission={setGooglePermission}
									/>
								</Col>
								<Col className='col3' xl={12}>
									<FacebookLogin
										appId='1068601560611480'
										autoLoad={false}
										fields='name,email,picture'
										scope='public_profile, publish_video'
										callback={props.responsefacebook}
										icon={<FaceboockIcon />}
										cssClass='facebook-button'
										textButton='Transmitir com Facebook'
									/>
								</Col>
								<Col className='col3' xl={12}>
									<Alert
										dismissible
										onClose={closeAlert}
										show={showAlert}
										variant={variantAlert}>
										{msgAlert}
									</Alert>
								</Col>
								<Col className='col3' xl={12}>
									<button
										className='login-button'
										type='submit'
										onClick={startTransmission}>
										Transmitir
									</button>
								</Col>
							</Row>
						</div>
					</Container>
				</Modal.Body>
			</Modal>
		</ThemeProvider>
	);
};

// temporary facebook logo
const FaceboockIcon = () => (
	<svg
		xmlns='http://www.w3.org/2000/svg'
		xmlnsXlink='http://www.w3.org/1999/xlink'
		style={{ width: 20, height: 20, marginRight: 8 }}
		role='img'
		preserveAspectRatio='xMidYMid meet'
		viewBox='0 0 750 850'>
		<path
			d='M659 8q34 0 59 24t24 58v577q0 35-24 59t-59 24H494V461h83l23-102H494v-69q0-22 13-41t44-19h49V123h-74q-79 0-117 42t-38 110v84h-83v102h83v289H82q-34 0-58-24T0 667V90q0-34 24-58T82 8h577z'
			fill='currentColor'></path>
	</svg>
);

