import React, { useState } from 'react';
import { Modal, Container, Row, Col, Spinner } from 'react-bootstrap';
import { ColorPicker, useColor } from 'react-color-palette';
import 'react-color-palette/lib/css/styles.css';
import { SelectButton } from '../styles/Styles';

import './addCamera.css';

export const PickerModal = (props) => {
	const [loading, setLoading] = useState(false);
	const [color, setColor] = useColor('hex', '#121212');

	return (
		<Modal
			// dialogClassName='modal-40w'
			{...props}
			// size="lg"
			aria-labelledby='contained-modal-title-vcenter'
			centered>
			<Modal.Header closeButton>
				{/* <Title>Selecionar Cor</Title> */}
			</Modal.Header>
			<Container className='pickerContainer'>
				<ColorPicker
					width={456}
					height={228}
					color={color}
					onChange={setColor}
					hideHSV
					hideRGB
				/>
				<SelectButton
					onClick={() => {
						console.log(color.hex);
						props.updateColor(color.hex);
					}}>
					Selecionar
				</SelectButton>
			</Container>
			;
		</Modal>
	);
};
