const INITIAL_STATE = {
	selected: {
		id: null,
		title: '',
		subtitle: '',
		colorBackground: '',
	},
};

const subtitle = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case '@subtitle/SELECT_SUBTITLE':
			return {
				...state,
				selected: action.subtitle,
			};

		case '@subtitle/REMOVE_SUBTITLE':
			return state;

		default:
			return state;
	}
};

export default subtitle;
