import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { Modal, Spinner } from 'react-bootstrap';

const Container = styled.div`
	user-select: none;
	width: 220px;
	background: ${({ theme }) => theme.colors.Background};
	margin-right: 48px;
	@media (max-width: 768px) {
		width: 100%;
		margin-right: 0;
	}
`;

const Header = styled.div`
	height: 65px; // normalize header
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 16px 0;
	span {
		font: normal normal 600 16px/25px Poppins;
		color: ${({ theme }) => theme.colors.Text1};
	}
	button {
		border: none;
		outline: none;
		padding: 4px 8px;
		border-radius: 5px;
		background: #e9e9e9;
		&:disabled {
			cursor: normal;
			svg {
				opacity: 0.4;
			}
		}
	}
`;

const GridView = styled.div`
	display: grid;
	grid-column-gap: 12px;
	grid-row-gap: 12px;
	grid-template-columns: repeat(auto-fill, 60px);
	justify-content: flex-start;
	max-height: 250px;
	overflow-y: scroll;
	padding-right: 8px;
	svg {
		fill: ${({ theme }) => theme.colors.Text1};
	}
`;

const Thumbnail = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	width: 60px;
	height: 60px;
	border-radius: 5px;
	/* overflow: hidden; */
	background-color: #e9e9e9;
	transition: opacity, transform ease 0.2s;
	border: ${({ selected, theme }) =>
		selected ? `solid 2px ${theme.colors.Select}` : 'none'} !important;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&:hover {
		/* box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.25); */
		/* transform: scale(1.025); */
	}
`;

const CurrentFile = styled.img`
	width: 100%;
	height: 100%;
`;

const Button = styled.button`
	display: flex;
	align-items: center;
	background-color: ${({ theme, variant }) =>
		variant === 'danger' ? '#FF4E4E' : theme.colors.Buttons1};
	width: 150px;
	border-radius: 10px;
	justify-content: center;
	margin-right: 15px;
	font: normal normal 600 16px/25px Poppins;
	letter-spacing: 0px;
	color: ${({ theme }) => theme.colors.Text2};
	border: none;
	outline: none;
	height: 40px;
	svg {
		margin-right: 6px;
	}
	&:hover {
		filter: brightness(120%) saturate(120%);
	}
`;

export default function CardFilesThumbnail({
	title,
	titleModal,
	items,
	isLoading,
	typeMedia,
	deleteButton,
	onDeleteImage,
	children,
}) {
	const [files, setFiles] = useState([]);
	const [show, setShow] = useState(false);
	const [currentFile, setCurrentFile] = useState({
		idx: null,
		url: '',
	});

	const openFile = (e, idx, url) => {
		setCurrentFile({ idx, url });
		setShow(true);
	};

	const closeFile = () => {
		setShow(false);
	};

	const deleteCurrentImage = (e, url) => {
		console.log(url);
		onDeleteImage(url);
		setShow(false);
	};

	useEffect(() => {
		return setFiles(items);
	}, [items]);

	return (
		<Container>
			<Header>
				<span>{title}</span>
				{deleteButton ? (
					<div>
						<FontAwesomeIcon icon={faTrashAlt} />
					</div>
				) : null}
			</Header>
			<GridView>
				{isLoading ? (
					<Spinner animation='border' size='sm' />
				) : (
					files?.map((ele, idx) => (
						<Thumbnail
							onClick={(e) => openFile(e, idx, ele)}
							key={idx}
							src={ele}>
							<img src={ele} alt={`img ${idx}`} />
						</Thumbnail>
					))
				)}
				{children}
			</GridView>
		</Container>
	);
}
