export function connectErrors(socket) {
	return {
		type: '@errors/CONNECT_ERRORS',
		socket,
	};
}

export function addError(error) {
	return {
		type: '@errors/ADD_ERROR',
		error,
	};
}

export function disconnectErrors(socket) {
	return {
		type: '@errors/DISCONNECT_ERRORS',
		socket,
	};
}
