export function fetchTheme(userId) {
	return {
		type: '@theme/FETCH_THEME',
		userId,
	};
}

export function fetchThemeSuccess(theme) {
	return {
		type: '@theme/FETCH_THEME_SUCCESS',
		theme,
	};
}

export function setTheme(newTheme, userId) {
	return {
		type: '@theme/SET_THEME',
		newTheme,
		userId,
	};
}

export function setThemeSuccess(theme) {
	return {
		type: '@theme/SET_THEME_SUCCESS',
		theme,
	};
}

export function resetTheme() {
	return {
		type: '@theme/RESET_THEME',
	};
}
