import { put, takeLatest, all } from 'redux-saga/effects';

/* eslint-disable */
// ! Eslint disable to prevent error missing yield on socket
function* _selectSubtitle({ socket, subtitle }) {
	try {
		// emit to server
		console.log('EMITIU SUBTITLE---->', subtitle);
		console.log('Com Socket---->', socket);

		socket.emit('selectSubtitle', subtitle);
	} catch (error) {
		console.log(error);
	}
}

export default all([takeLatest('@subtitle/SELECT_SUBTITLE', _selectSubtitle)]);
