import React, { useState } from 'react';
import { Modal, Row, Col, Spinner } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import * as api from '../Services/api';

import './addCamera.css';

export const AddCamera = (props) => {
	const [loading, setLoading] = useState(false);

	const handleSubmit = async (newCam) => {
		const response = await api.createCam(newCam);
		if (!response.error) {
			console.log('response in Management->', response);
			if (response) {
				setLoading(false);
				props.onHide();
				//mensagem de sucesso
			}
		} else {
			setLoading(false);
			console.log(response.error.message);
			props.onHide();
			//mensagem de erro
		}
	};

	//   const Ref1 = useRef();
	//   const Ref2 = useRef();

	return (
		<Modal
			scrollable
			// dialogClassName='modal-40w'
			{...props}
			// size='lg'
			aria-labelledby='contained-modal-title-vcenter'
			centered>
			<Modal.Body>
				<div className='closebox-modal'>
					<FontAwesomeIcon icon={faTimes} onClick={(e) => props.onHide()} />
				</div>
				<form
					className='Form-addCamera'
					autocomplete='off'
					onSubmit={(event) => {
						event.preventDefault();
						setLoading(true);
						const elements = event.target.elements;
						console.log('submit!');
						const newCam = {
							name: elements.nome.value,
							url: elements.url.value,
							owner: props.User.id,
							username: elements.username.value,
							ip: elements.ip.value,
							password: elements.password.value,
						};
						handleSubmit(newCam);
					}}>
					<Row className='row-addCam'>
						<Col xl={12} className='col-addCam-title header-modal'>
							<h1 className={'title-addCam'}>Cadastrar nova câmera</h1>
						</Col>
						<Col xl={12} className='col-addCam-input'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Nome
								</label>
								<input
									name='nome'
									required
									className='inputs-addUser'
									placeholder='Nome'></input>
							</div>
						</Col>
						<Col xl={12} className='col-addCam-input'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Stream url
								</label>
								<input
									name='url'
									required
									className='inputs-addUser'
									placeholder='rtsp://000.000.000.0'></input>
							</div>
						</Col>
						{/* username */}
						<Col xl={12} className='col-addCam-input'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Usuário
								</label>
								<input
									name='username'
									required
									className='inputs-addUser'
									placeholder='admin'></input>
							</div>
						</Col>
						{/* ip */}
						<Col xl={12} className='col-addCam-input'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Endereço IP
								</label>
								<input
									name='ip'
									required
									className='inputs-addUser'
									placeholder='192.168.0.1'></input>
							</div>
						</Col>
						{/* password */}
						<Col xl={12} className='col-addCam-input'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Senha
								</label>
								<input
									name='password'
									required
									className='inputs-addUser'
									type='password'
									placeholder='senha'></input>
							</div>
						</Col>
						<Col xl={12} className='col-addCam-button'>
							{loading ? (
								<Spinner animation='border' size='sm' />
							) : (
								<button type='submit' className='login-button'>
									Cadastrar
								</button>
							)}
						</Col>
					</Row>
				</form>
			</Modal.Body>
		</Modal>
	);
};
