import React, { useState } from 'react';
import { connect } from 'react-redux';
import { ThemeProvider } from 'styled-components';
import Sidebar from '../components/Sidebar';
import { Title } from '../styles/Styles';
import ButtonLogout from '../components/MyButton';

import { Container, Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars } from '@fortawesome/free-solid-svg-icons';

// notifications component
import Notification from '../components/Notification';
import MobileMenu from '../components/MobileMenu';

const MainLayout = ({ children, title, theme }) => {
	return (
		<ThemeProvider theme={theme}>
			<Container
				style={{ backgroundColor: theme.colors.Background }}
				fluid
				className='app_container'>
				<Row className='sidebar-row'>
					<Col xl={2} className='container-sidebar'>
						<Sidebar />
						<MobileMenu />
					</Col>
					<Col xl={10} className='page'>
						<Row className='row-nav'>
							{/* 
              <Col xl={6} fluid className='container-searchbar'>
							</Col> 
              */}
							<Col className='bell'>
								<Notification />
							</Col>
							<Col xl={2} className='container-button header-control'>
								<ButtonLogout buttonStyle='btn--user' className='user-button'>
									Sair
								</ButtonLogout>
							</Col>
						</Row>
						{/* title e views */}
						{children}
					</Col>
				</Row>
			</Container>
		</ThemeProvider>
	);
};

const mapStateToProps = (state) => ({
	theme: state.theme,
});

export default connect(mapStateToProps)(MainLayout);
