import React, { useState } from 'react';
import styled from 'styled-components';
import Navbar from '../components/Navbar';
import { Alert } from 'react-bootstrap';

import * as api from '../Services/api';

import './Login.css';

const Container = styled.div`
	background: #fff;

	.form {
		padding: 80px 0 150px 80px;
		text-align: center;
		letter-spacing: 0.25px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		color: #121212;
		h1 {
			font: normal normal 800 25px/35px Poppins;
		}
		h4 {
			font: normal normal 600 16px/18px Poppins;
		}
		.field {
			display: flex;
			flex-direction: column;
			padding: 32px 16px;
			label {
				text-align: left;
				font: normal normal bold 12px/18px Poppins;
				letter-spacing: 0px;
			}
		}
		.submit-reset {
			padding: 0 16px;
		}
	}

	@media (max-width: 768px) {
		.form {
			padding: 100px 20px;
		}
	}
`;

export default function ResetPassword() {
	const [email, setEmail] = useState('');
	const [variantAlert, setVariantAlert] = useState('');
	const [msgAlert, setMsgAlert] = useState('');
	const [showAlert, setShowAlert] = useState(false);

	// validating email
	const validateEmail = (email) => {
		const re =
			/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return re.test(String(email).toLowerCase());
	};

	// send request
	const requestReset = async () => {
		if (!email || !validateEmail(email)) {
			setVariantAlert('danger');
			setMsgAlert('E-mail inválido.');
			setShowAlert(true);
		} else {
			const res = await api.resetPassword(email);
			if (res) {
				setVariantAlert('success');
				setMsgAlert('Solicitação enviada com sucesso.');
				setShowAlert(true);
			} else {
				setVariantAlert('danger');
				setMsgAlert('Erro ao enviar a solicitação.');
				setShowAlert(true);
			}
		}
	};

	const closeAlert = () => {
		setShowAlert(false);
	};

	return (
		<Container>
			<Navbar type='navbar-login' />
			<div className='form'>
				<h1>Redefinição de Senha</h1>
				<h4>Informe o e-mail para redefinir sua senha.</h4>
				<div className='field'>
					<label>E-mail</label>
					<input
						className='input1'
						value={email}
						onChange={(e) => setEmail(e.target.value)}
					/>
				</div>
				<div className='submit-reset'>
					<button className='login-button' onClick={requestReset}>
						Enviar
					</button>
				</div>
				<div className='field'>
					<Alert
						onClose={closeAlert}
						show={showAlert}
						variant={variantAlert}
						dismissible>
						{msgAlert}
					</Alert>
				</div>
			</div>
		</Container>
	);
}
