import React from 'react';
import { Modal, Col, Row, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import './Details.css';

export const Details = (props) => {
	return (
		<Modal
			{...props}
			// dialogClassName="modal-40w"
			size='lg'
			scrollable
			aria-labelledby='contained-modal-title-vcenter'
			centered>
			<Modal.Body>
				<div className='closebox-modal'>
					<FontAwesomeIcon
						icon={faTimes}
						// onClick={(e) => props.OnHide()}
					/>
				</div>
				<Container className='modal-details'>
					<form autocomplete='off' className='form-Details'>
						<Row>
							<Col className='header-modal'>
								<h1 className='title-details'>Detalhes do usuário</h1>
							</Col>
						</Row>
						<Row className='rows-Details'>
							<Col xl={6}>
								<div className='grouping'>
									<label className='labels-contrato' htmlfor='nome'>
										Nome
									</label>
									<h1 className='det-headers'>{props.UserInfo.name}</h1>
								</div>
							</Col>
							<Col xl={6}>
								<div className='grouping'>
									<label className='labels-contrato' htmlfor='nome'>
										Telefone
									</label>

									<h1 className='det-headers'>{props.UserInfo.phone}</h1>
								</div>
							</Col>
						</Row>

						<Row className='rows-Details'>
							<Col xl={6}>
								<div className='grouping'>
									<label className='labels-contrato' htmlfor='CPF'>
										CPF/CNPJ
									</label>
									<h1 className='det-headers'>{props.UserInfo.document}</h1>
								</div>
							</Col>
							<Col xl={6}>
								<div className='grouping'>
									<label className='labels-contrato' htmlfor='nome'>
										Endereço
									</label>
									<h1 className='det-headers'>{props.UserInfo.cep}</h1>
									<h1 className='det-headers'>{props.UserInfo.address}</h1>
									<h1 className='det-headers'>{props.UserInfo.city}</h1>
								</div>
							</Col>
						</Row>

						<Row className='rows-Details'>
							<Col xl={6} className='col-email'>
								<div className='grouping'>
									<label className='labels-contrato' htmlfor='nome'>
										E-mail
									</label>
									<h1 className='det-headers'>{props.UserInfo.email}</h1>
								</div>
							</Col>

							<Col xl={6} className='col3'></Col>
						</Row>
					</form>
				</Container>
			</Modal.Body>
		</Modal>
	);
};
