/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import ButtonLogout from '../components/MyButton';
// import { Sidebar } from '../components/Sidebar';
import Sidebar from '../components/Sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPen } from '@fortawesome/free-solid-svg-icons';
import { faBell } from '@fortawesome/free-regular-svg-icons';
import { AddCamera } from '../components/addCamera';
import { EditCamera } from '../components/EditCamera ';
import * as api from '../Services/api';
import { Title, Subtitle, AddButton } from '../styles/Styles';
import {
	CamCardTitle,
	CamCardLabel,
	CamCardPar,
	CamCardButon,
	PenButon,
} from '../styles/Styles';
import './Cameras.css';
import { Container, Row, Col, Button, Spinner } from 'react-bootstrap';

// andré: conecta Camera a store
import { connect } from 'react-redux';

const Cameras = ({ user: User, theme }) => {
	const location = useLocation();
	// const [User] = useState(location.state);
	const [CamsArray, setCamsArray] = useState(null);
	// const theme = location.theme;
	const [show, setShow] = useState(false);
	const [update, setUpdate] = useState(false);
	const [loading, setLoading] = useState(false);

	const getCameras = async () => {
		setLoading(true);
		const response = await api.getCams(User.id);
		if (!response.error) {
			console.log('response in Cameras->', response);
			setCamsArray(response);
			setLoading(false);
		} else {
			console.log(response.error.message);
			setLoading(false);
		}
	};

	useEffect(() => {
		getCameras();
		setUpdate(false);
	}, [show, update]);

	const Item = (props) => {
		console.log('cams ->', props);
		const [edit, setEdit] = useState(false);
		return (
			<>
				<Container
					style={{ backgroundColor: theme.colors.Select }}
					fluid
					className='card-cam'>
					<Row fluid className='card-title-row'>
						<Col fluid className='card-title-col' xl={8}>
							<CamCardTitle>{props.name}</CamCardTitle>
						</Col>
						<Col className='pen-col' xl={4}>
							<PenButon onClick={() => setEdit(true)}>
								<FontAwesomeIcon icon={faPen} style={{ color: 'white' }} />
							</PenButon>
						</Col>
					</Row>
					<Row>
						<Col fluid>
							<CamCardLabel>Url:</CamCardLabel>
							<CamCardPar>{props.url || '-'}</CamCardPar>
							<CamCardLabel>Usuário:</CamCardLabel>
							<CamCardPar>{props.username || '-'}</CamCardPar>
							<CamCardLabel>Endereço IP:</CamCardLabel>
							<CamCardPar>{props.ip || '-'}</CamCardPar>
							<CamCardLabel>Status:</CamCardLabel>
							<CamCardButon>Normal</CamCardButon>
						</Col>
					</Row>
				</Container>
				<EditCamera
					show={edit}
					onHide={() => {
						setEdit(false);
						setUpdate(true);
					}}
					camInfo={{
						name: props.name,
						url: props.url,
						id: props.id,
						username: props.username,
						ip: props.ip,
					}}
				/>
			</>
		);
	};

	const renderItem = (data) => {
		if (!data) {
			return null;
		}
		const array = [];
		var i = 0;
		for (i; i < data.length; i++) {
			array.push(
				<Item
					name={data[i].name}
					url={data[i].url}
					id={data[i].id}
					username={data[i].username}
					ip={data[i].ip}
				/>
			);
		}
		return array;
	};

	return (
		<div style={{ paddingBottom: 50 }} className='fade-in'>
			<Row className='title-row'>
				<Col xl={6} className='title-col'>
					<Title>Câmeras cadastradas</Title>
				</Col>
				<Col xl={6} className='button-col'>
					<AddButton onClick={() => setShow(true)}>
						Cadastrar nova
						<i className='fas fa-video cam-btn'></i>
					</AddButton>
				</Col>
			</Row>
			<Row>
				<Container fluid className='container-cam main_container'>
					{loading ? (
						<Spinner animation='border' size='sm' />
					) : (
						renderItem(CamsArray)
					)}
				</Container>
			</Row>
			<AddCamera show={show} User={User} onHide={() => setShow(false)} />
		</div>
	);
};

const mapStateToProps = (state) => ({
	user: state.user.profile,
	theme: state.theme,
});

export default connect(mapStateToProps)(Cameras);
