import React, { useState, useEffect, forwardRef } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { Dropdown } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Button } from '../styles/Styles';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { selectCamera } from '../store/modules/camera/actions';

const { Toggle, Header, Menu, Item } = Dropdown;

// font styled
const DropdownButton = styled(Dropdown)`
	font-family: Poppins, sans-serif;
	font-weight: 600;
	font-size: 16px;
	color: ${({ theme }) => theme.colors.Text1};
	@media (max-width: 768px) {
		margin-bottom: 16px;
		& > button {
			width: 100%;
			margin-right: 0 !important;
		}
	}
`;

// toggle personalizado
const RCODropdownToggle = forwardRef(({ children, onClick, disabled }, ref) => (
	<Button disabled={disabled}
		ref={ref}
		onClick={(e) => {
			e.preventDefault();
			onClick(e);
		}}>
		{children}
	</Button>
));

// component principal
export const DropdownCameras = ({
	disabled,
	items, // array of items
	camera, // selected camera from redux
	dispatch, // event to change camera on redux
	socket,
}) => {
	const toggleSelect = (item) => {
		// if (item.id === camera.id) return;
		dispatch(selectCamera(socket, item));
	};

	return (
		<DropdownButton >
			<Toggle disabled={disabled} as={RCODropdownToggle}>
				<span>Trocar câmera</span>
				<FontAwesomeIcon icon={faArrowRight} />
			</Toggle>
			<Menu>
				<Header>
					{items.length ? 'Câmeras disponíveis' : 'Nenhuma câmera disponível'}
				</Header>
				{items.length
					? items.map((ele) => (
							<Item
								onClick={(e) => toggleSelect(ele)}
								active={camera.id === ele.id}
								key={ele.id}
								eventKey={ele.id}>
								{ele.name}
							</Item>
					  ))
					: null}
			</Menu>
		</DropdownButton>
	);
};

const mapStateToProps = (state) => ({
	camera: state.camera.selected,
});

export default connect(mapStateToProps)(DropdownCameras);
