const INITIAL_STATE = {
	on: false,
	Gtoken: '',
	GViews: [],
	FBtoken: '',
	FBViews: [],
	liveIds: {
		facebookLiveId: '',
		youtubeLiveId: '',
	},
};

const streaming = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case '@streaming/STREAMING_START':
			const { data, liveIds } = action;
			return {
				...state,
				on: true,
				Gtoken: data.Gtoken,
				FBtoken: data.FBtoken,
				liveIds: {
					facebookLiveId: liveIds.facebookLiveId,
					youtubeLiveId: liveIds.youtubeLiveId,
				},
			};

		case '@streaming/STREAMING_STOP':
			return {
				...state,
				on: false,
				FBViews: [],
				GlViews: [],
			};

		case '@streaming/STREAMING_ERROR':
			return {
				...state,
				on: false,
			};

		case '@streaming/STREAMING_UPDATE_VIEW_FB':
			const { viewsFB } = action;
			const live_viewsFB = Number(viewsFB.live_views);
			const idFB = viewsFB.id;
			// normalize schema for chart audience component
			const dataViewFB = {
				id: idFB,
				live_views: live_viewsFB,
			};
			return {
				...state,
				FBViews: [...state.FBViews, dataViewFB],
			};

		case '@streaming/STREAMING_UPDATE_VIEW_GL':
			const { viewsGL } = action;
			const live_viewsGL = Number(viewsGL.statistics.viewCount);
			const idGL = viewsGL.id;
			// normalize schema for chart audience component
			const dataViewGL = {
				id: idGL,
				live_views: live_viewsGL,
			};
			return {
				...state,
				GViews: [...state.GViews, dataViewGL],
			};

		default:
			return {
				...state,
			};
	}
};

export default streaming;
