import React, { useState, useEffect, useRef } from 'react';
import { Modal, Col, Row, Container, Button, Spinner } from 'react-bootstrap';
import InputMask from 'react-input-mask';
import { FileUploader } from '../components/FileUploader';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import * as api from '../Services/api';
import './EditUser.css';

function validatePassword() {
	var Pass = document.getElementById('password-field'),
		confirm = document.getElementById('password-confirmation');
	if (Pass.value !== confirm.value) {
		confirm.setCustomValidity('Confirmação incorreta.');
		confirm.checkValidity();
		return false;
	} else {
		confirm.setCustomValidity('');
		return true;
	}
}

export const EditUser = (props) => {
	const emailField = useRef(null);
	const [req, setReq] = useState(false);
	const confpass = useRef(null);
	const [loading, setLoading] = useState(false);
	const [doc, setDocument] = useState(props.UserInfo.document);
	const [address, setAddress] = useState(props.UserInfo.address);
	const [city, setCity] = useState(props.UserInfo.city);
	const [uf, setUf] = useState(props.UserInfo.uf);
	const [selectedFile, setSelectedFile] = useState(null);

	const onFileSelectSuccess = (file) => {
		console.log('File Selected!');
		console.log(file);
		setSelectedFile(file);
	};

	const onFileSelectError = (error) => {
		console.log('error-> ', error);
	};

	const handleSubmit = async (formdata, id) => {
		const data = formdata;
		if (selectedFile) {
			data.append('file', selectedFile);
			data.append('filename', selectedFile.name);
			data.append('path', 'images');
		}
		console.log('data being passed->', data);
		const response = await api.editUser(data, id);
		if (!response.error) {
			console.log('response in Management->', response);
			if (response) {
				setLoading(false);
				props.onHide();
				//mensagem de sucesso
			}
		} else {
			setLoading(false);
			console.log(response.error.message);
			props.onHide();
			//mensagem de erro
		}
		setSelectedFile(null);
	};

	const handleAutoCompleteCep = async (value) => {
		const cep = value.replace(/[^0-9]/g, '');

		if (cep.length !== 8) {
			return;
		}

		const response = await api.getViaCep(cep);

		if (response.erro) {
			return;
		}
		setAddress(response.logradouro);
		setCity(response.localidade);
		setUf(response.uf);
	};

	const validateEmail = async (email) => {
		const response = await api.checkUser({ email: email });
		if (!response.error) {
			return true;
		} else {
			return false;
		}
	};

	useEffect(() => {}, []);

	return (
		<Modal
			// dialogClassName='modal-80w'
			{...props}
			size='lg'
			scrollable
			aria-labelledby='contained-modal-title-vcenter'
			centered>
			<Modal.Body>
				<div className='closebox-modal'>
					<FontAwesomeIcon
						style={{ fontSize: 18 }}
						icon={faTimes}
						onClick={(e) => props.onHide()}
					/>
				</div>
				{/* <Container> */}
				<form
					className='Form-addUser'
					autoComplete='off'
					onSubmit={async (event) => {
						event.preventDefault();
						setLoading(true);
						const elements = event.target.elements;
						const formdata = new FormData();
						formdata.append('name', elements.name.value);
						formdata.append('email', elements.email.value);
						formdata.append('document', elements.document.value);
						formdata.append('phone', elements.tel.value);
						formdata.append('address', elements.address.value);
						formdata.append('st_number', elements.stNum.value);
						formdata.append('city', elements.city.value);
						formdata.append('uf', elements.state.value);
						formdata.append('cep', elements.cep.value);
						if (elements.password.value.length >= 1) {
							formdata.append('password', elements.password.value);
							formdata.append(
								'password_confirmation',
								elements.password_confirm.value
							);
						}

						formdata.append('id', props.UserInfo.id);
						// const newUser = {
						//   name: elements.name.value,
						//   email: elements.email.value,
						//   document: elements.document.value,
						//   phone: elements.tel.value,
						//   address: elements.address.value,
						//   st_number: elements.stNum.value,
						//   city: elements.city.value,
						//   uf: elements.state.value,
						//   cep: elements.cep.value,
						//   password: elements.password.value,
						//   password_confirmation: elements.password_confirm.value,
						//   id: props.UserInfo.id,
						//   inicio: "14/06/2021",
						//   vencimento: "14/06/2022",
						// };

						const response = await validateEmail(elements.email.value);
						console.log('result->', response);
						if (response === true) {
							console.log('validity Checked email');
							emailField.current.setCustomValidity('');
							if (validatePassword() === true) {
								console.log('validity Checked password');
								handleSubmit(formdata, props.UserInfo.id);
								setDocument('');
								setAddress('');
								setUf('');
								setCity('');
								setLoading(false);
							} else setLoading(false);
						} else if (response === false) {
							if (elements.email.value == props.UserInfo.email) {
								if (validatePassword() === true) {
									console.log('validity Checked password');
									handleSubmit(formdata, props.UserInfo.id);
									setDocument('');
									setAddress('');
									setUf('');
									setCity('');
									setLoading(false);
								} else setLoading(false);
							} else {
								setLoading(false);
								emailField.current.setCustomValidity(
									'Este e-mail já esta sendo utilizado.'
								);
							}
						}
					}}>
					<Row>
						<Col xl={12} className='col3 header-modal'>
							<h1 className='title-modal'>Editar usuário</h1>
						</Col>
						<Col xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Nome
								</label>
								<input
									type='text'
									name='name'
									autoComplete='false'
									className='inputs-addUser'
									placeholder='Nome'
									defaultValue={props.UserInfo.name}
									required></input>
							</div>
						</Col>
						<Col xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									E-mail
								</label>
								<input
									id='email-field'
									type='email'
									name='email'
									required
									className='inputs-addUser'
									placeholder='E-mail'
									ref={emailField}
									defaultValue={props.UserInfo.email}
									onChange={() =>
										emailField.current.setCustomValidity('')
									}></input>
							</div>
						</Col>

						<Col xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='CPF'>
									CPF/CNPJ
								</label>
								<InputMask
									mask={
										doc.length < 15 ? '999.999.999-999' : '99.999.999/9999-99'
									}
									maskChar=''
									name='document'
									value={doc}
									onChange={(e) => setDocument(e.target.value)}
									required
									className='inputs-addUser'
									placeholder='CPF/CNPJ'
									defaultValue={props.UserInfo.document}></InputMask>
							</div>
						</Col>
						<Col xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Telefone
								</label>
								<InputMask
									mask='(99)99999-9999'
									name='tel'
									type='string'
									required
									className='inputs-addUser'
									placeholder='Telefone'
									defaultValue={props.UserInfo.phone}></InputMask>
							</div>
						</Col>

						<Col xl={4} className='col2'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Endereço
								</label>
								<input
									type='text'
									id='address-field'
									name='address'
									required
									className='inputs-endereco'
									placeholder='Endereço'
									value={address}
									onChange={(event) => setAddress(event.target.value)}></input>
							</div>
						</Col>

						<Col xl={2} className='col2'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									N°
								</label>
								<input
									type='text'
									name='stNum'
									className='inputs-numero'
									placeholder='N°'
									defaultValue={props.UserInfo.st_number}></input>
							</div>
						</Col>

						<Col xl={6} className='col1'>
							<div className='grouping'>
								<label className='labels-contrato'>Foto de Perfil</label>
								<FileUploader
									cName={'inputs-foto'}
									onFileSelectSuccess={onFileSelectSuccess}
									onFileSelectError={onFileSelectError}
									sizeLimit={10485760} //Tamanho limite de 10MB
									text={'Adicionar foto'}
									variant='text-button'
								/>
								{/* <Button variant="light" className="inputs-foto">
                    Adicionar foto
                    <i class="fas fa-plus fplus"></i>
                  </Button> */}
							</div>
						</Col>

						<Col xl={6} className='col-bairro'>
							<div className='grouping'>
								<label className='labels-contrato'>Cidade</label>
								<input
									type='text'
									name='city'
									id='city-field'
									className='inputs-addUser'
									placeholder='Bairro/Cidade'
									required
									value={city}
									onChange={(event) => setCity(event.target.value)}></input>
							</div>
						</Col>
						<Col xl={6}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Senha
								</label>
								<input
									id='password-field'
									type='password'
									name='password'
									className='inputs-addUser'
									placeholder='Senha'
									required={req}
									defaultValue={props.UserInfo.password}
									onChange={(event) => {
										validatePassword();
									}}></input>
							</div>
						</Col>

						<Col xl={4} className='col4'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Estado
									<input
										type='text'
										name='state'
										id='state-field'
										className='inputs-endereco'
										placeholder='Estado'
										required
										defaultValue={props.UserInfo.uf}
										value={uf}
										onChange={(event) => setUf(event.target.value)}></input>
								</label>
							</div>
						</Col>
						<Col className='col5' xl={2}>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									CEP
								</label>
								<InputMask
									mask='99999-999'
									type='text'
									name='cep'
									className='inputs-numero'
									placeholder='CEP'
									defaultValue={props.UserInfo.cep}
									onBlur={(event) => handleAutoCompleteCep(event.target.value)}
									required></InputMask>
							</div>
						</Col>
						<Col xl={6} className='col6'>
							<div className='grouping'>
								<label className='labels-contrato' htmlfor='nome'>
									Confirmar Senha
									<input
										type='password'
										id='password-confirmation'
										name='password_confirm'
										className='inputs-addUser'
										placeholder='Senha'
										ref={confpass}
										required={req}
										onChange={(event) => {
											confpass.current.setCustomValidity('');
											if (event.target.value.length >= 1) {
												setReq(true);
											} else setReq(false);
											console.log(req);
											console.log(req);
											validatePassword();
										}}
										defaultValue={props.UserInfo.password}></input>
								</label>
							</div>
						</Col>

						<Col className='col3' xl={12}>
							{loading ? (
								<Spinner animation='border' size='sm' />
							) : (
								<button className='login-button' type='submit'>
									Editar
								</button>
							)}
						</Col>
					</Row>
				</form>
				{/* </Container> */}
			</Modal.Body>
		</Modal>
	);
};
