import { eventChannel, END } from 'redux-saga';
import { put, takeLatest, all, call, take } from 'redux-saga/effects';
import { updateViewsFB, streamingStop, updateViewsGL } from './actions';

import axios from 'axios';

// youtube api key
// const YOUTUBE_API_KEY = process.env.YOUTUBE_API_KEY;
import apiKey from '../../../YouTubeAPIKey.json';
const { YOUTUBE_API_KEY } = apiKey;

// facebook views
function watchViews({ data, liveIds }) {
	return eventChannel((emitter) => {
		const iv = setInterval(async function () {
			if (data && liveIds) {
				// facebook views
				if (liveIds.facebookLiveId) {
					const viewsFB = await axios.get(
						`https://graph.facebook.com/${liveIds.facebookLiveId}/?fields=live_views&access_token=${data.FBtoken}`,
						{ withCredentials: false }
					);
					// check response
					if (viewsFB.data.id) {
						console.log('sagas-FBviews', viewsFB.data);
						emitter(updateViewsFB(viewsFB.data));
					}
				}
				// youtube views
				// for more info see https://developers.google.com/youtube/v3/docs/videos
				if (liveIds.youtubeLiveId) {
					const viewsGL = await axios.get(
						`https://www.googleapis.com/youtube/v3/videos?id=${liveIds.youtubeLiveId}&key=${YOUTUBE_API_KEY}&fields=items(id,statistics)&part=statistics`,
						{ withCredentials: false }
					);
					// check response
					if (viewsGL.data.items.length > 0) {
						console.log('sagas-GLviews', viewsGL.data.items[0]);
						emitter(updateViewsGL(viewsGL.data.items[0]));
					}
				}
			} else {
				emitter(END);
			}
		}, 10000);
		return () => {
			clearInterval(iv);
			emitter(streamingStop());
		};
	});
}

function* _streamingStart({ data, liveIds }) {
	console.log('streamingOn', true);
	const channel = yield call(watchViews, { data, liveIds });
	try {
		while (true) {
			let action = yield take(channel);
			yield put(action);
		}
	} catch (error) {
		console.log('streaming', error);
	}
}

export default all([takeLatest('@streaming/STREAMING_START', _streamingStart)]);
