import React, { useState, useEffect } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import ButtonLogout from '../components/MyButton';
import { Title, Subtitle, AddButton } from '../styles/Styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBell } from '@fortawesome/free-regular-svg-icons';
import { List } from '../components/List';
import { MyCenteredModal } from '../components/MyCenteredModal';
import { AddUser } from '../components/AddUser';
// import { Sidebar } from '../components/Sidebar';
import Sidebar from '../components/Sidebar';
import * as api from '../Services/api';
import {
	Container,
	Row,
	Col,
	InputGroup,
	FormControl,
	Button,
	Spinner,
} from 'react-bootstrap';
import './Management.css';

// andré: conecta Management a store
import { connect } from 'react-redux';

const Management = ({ user: User, theme }) => {
	const location = useLocation();
	const history = useHistory();
	const [modalShow, setModalShow] = useState(false);
	const [addUserModal, setAddUserModal] = useState(false);
	// const [User] = useState(location.state);
	// const theme = location.theme;
	const [loading, setLoading] = useState(false);
	const [UsersArray, setUsersArray] = useState(null);
	const [update, setUpdate] = useState(0);

	const getUsers = async () => {
		setLoading(true);
		const response = await api.getUsers();
		if (!response.error) {
			console.log('response in Management->', response);
			setUsersArray(response);
			setLoading(false);
		} else {
			console.log(response.error.message);
			setLoading(false);
		}
	};

	useEffect(() => {
		if (!User || User.role != 'admin') {
			history.push('/login');
		} else {
			getUsers();
		}
	}, [addUserModal, update]);

	return (
		<div style={{ paddingBottom: 50 }} className='fade-in'>
			<Row className='title-row'>
				<Col xl={6} className='title-col'>
					<Title>Usuários</Title>
				</Col>
				<Col xl={6} className='button-col'>
					<AddButton
						onClick={() => {
							setAddUserModal(true);
						}}>
						Cadastrar Usuário
						<i class='fas fa-user-plus'></i>
					</AddButton>
				</Col>
			</Row>
			<Row>
				<Container fluid className='main_container'>
					{loading ? (
						<Spinner animation='border' size='sm' />
					) : (
						<List
							Data={UsersArray || []}
							setModalShow={setModalShow}
							setUpdate={setUpdate}
							theme={theme}
						/>
					)}
				</Container>
			</Row>
			<AddUser show={addUserModal} setAddUserModal={setAddUserModal}></AddUser>
		</div>
	);
};

const mapStateToProps = (state) => ({
	user: state.user.profile,
	theme: state.theme,
});

export default connect(mapStateToProps)(Management);
