/* eslint-disable eqeqeq */
import React, { useState, useEffect, useContext } from 'react';
import { ThemeContext } from 'styled-components';
import { cfg } from '../Services/api';
import Logo from '../assets/Logo.png';
import {
	// SideButton,
	// SelectedButton,
	SelectedButtonLink,
	// TransmissionButton,
} from '../styles/Styles';
import { useLocation, useHistory } from 'react-router-dom';
import { Container, Button, Image } from 'react-bootstrap';
import { Icon } from './Icons';
import './Sidebar.css';

// andré: conecta o Sidebar a store
import { connect } from 'react-redux';

const logo = require('../assets/Logo.png');

// andré: navlink para detectar a rota atual
const SelectLink = ({ children, selected, type, route }) => {
	const { pathname } = useLocation();
	const [active, setActive] = useState(false);

	useEffect(() => {
		setActive(route === pathname);
	}, [route, pathname]);

	return (
		<SelectedButtonLink type={type} selected={active || selected} to={route}>
			{children}
		</SelectedButtonLink>
	);
};

const Sidebar = ({ user: User, theme }) => {
	const [isMobile, setIsMobile] = useState(false);

	useEffect(() => {
		const width = window.screen.width;
		if (width <= 768) {
			setIsMobile(true);
		}
	}, []);

	if (isMobile) return null;

	return (
		<Container
			className='sidebar'
			style={{
				backgroundColor: theme.colors.Background,
				alignItems: 'center',
			}}>
			{!User.avatar ? (
				<Image src={Logo} className='logo' rounded fluid />
			) : (
				<Image
					src={cfg.API_ROOT + User.avatar}
					className='logo'
					rounded
					fluid
				/>
			)}
			{/* andré: implantação do navlink */}
			<SelectLink route='/home'>
				<Icon type='home' />
				Home
			</SelectLink>

			{User.role === 'admin' ? (
				<SelectLink route='/management'>
					<Icon type='tv' />
					Gerenciar Usuários
				</SelectLink>
			) : null}

			<SelectLink route='/cameras'>
				<Icon type='camera' />
				Câmeras
			</SelectLink>

			<SelectLink route='/files'>
				<Icon type='folder' />
				Arquivos
			</SelectLink>

			<SelectLink type='secondary' route='/transmission'>
				<Icon type='plus' />
				Nova Transmissão
			</SelectLink>
		</Container>
	);
};

const mapStateToProps = (state) => ({
	user: state.user.profile,
	theme: state.theme,
});

export default connect(mapStateToProps)(Sidebar);
