import { all } from 'redux-saga/effects';

import user from './user/sagas';
import theme from './theme/sagas';
import subtitle from './subtitle/sagas';
import camera from './camera/sagas';
import errors from './errors/sagas';
import streaming from './streaming/sagas';

export default function* rootSagas() {
	return yield all([user, theme, subtitle, camera, errors, streaming]); //
}
