import React, { useEffect, useState } from 'react';
import {
	Row,
	Col,
	ListGroup,
	ListGroupItem,
	Button,
	Image,
	FormCheck,
	Dropdown,
} from 'react-bootstrap';
import {
	Title,
	Subtitle,
	AddButton,
	TableTitle,
	TableSubtitle,
} from '../styles/Styles';
import './List.css';
import { Details } from './Details';
import { EditUser } from './EditUser';
import { MyCenteredModal } from '../components/MyCenteredModal';
import Logo from '../assets/Logo.png';
import * as api from '../Services/api';

export const List = (props) => {
	// insert a pagination
	const [items, setItems] = useState([]);
	const [page, setPage] = useState(1);

	function pagination(array, limit, page) {
		return array.slice((page - 1) * limit, page * limit);
	}

	function numberOfPages(array) {
		let k = array.length / 5;
		k = k.toFixed(1).toString().split('.');
		if (Number(k[1]) > 0) {
			return Number(k[0]) + 1;
		}
		return Number(k[0]);
	}

	useEffect(() => {
		// console.log('Data in List', props.Data);
		setItems(props.Data);
	}, [props.Data]);

	const renderItem = (data) => {
		if (!data) {
			return null;
		}
		const array = [];
		var i = 0;
		for (i; i < data.length; i++) {
			array.push(
				<ListGroupItem className='list-item-container'>
					<Item
						User={data[i]}
						btnID={i}
						setModalShow={props.setModalShow}
						setUpdate={props.setUpdate}
					/>
				</ListGroupItem>
			);
		}
		return array;
	};

	const Item = (props) => {
		const [showDetails, setShowDetails] = useState(false);
		const [showEdit, setShowEdit] = useState(false);
		const [showDate, setShowDate] = useState(false);
		const [suspended, setSuspended] = useState(props.User.suspended);
		let cont = 1;

		const del = async () => {
			const response = await api.deleteUser(props.User.id);
			console.log(response);
			cont = Math.random();
			props.setUpdate(cont);
		};

		const update = async (value, id) => {
			const formdata = new FormData();
			if (value === true) formdata.append('suspended', 1);
			else formdata.append('suspended', 0);
			const response = await api.editUser(formdata, id);
			console.log(response);
			cont = Math.random();
			props.setUpdate(cont);
		};

		return (
			<Row className='item-Rows'>
				<Col xl={3} className='item-cols'>
					<div className='avatar-div'>
						{!props.User.avatar ? (
							<Image src={Logo} className='avatar' roundedCircle fluid />
						) : (
							<Image
								src={api.cfg.API_ROOT + props.User.avatar}
								className='avatar'
								roundedCircle
								fluid
							/>
						)}
						<div className='username-div'>
							<TableSubtitle>{props.User.name}</TableSubtitle>
						</div>
					</div>
				</Col>

				<Col xl={2} className='item-cols'>
					<Button
						className='switch-button'
						variant='light'
						bsPrefix='btn'
						onClick={() => setShowDate(true)}>
						<i class='far fa-calendar-alt'></i>
						<TableSubtitle>{props.User.inicio}</TableSubtitle>
					</Button>
				</Col>

				<Col xl={3} className='item-cols'>
					<Button
						variant='light'
						bsPrefix='btn'
						className='switch-button'
						onClick={() => setShowDate(true)}>
						<i class='far fa-calendar-alt'></i>
						<TableSubtitle>{props.User.vencimento}</TableSubtitle>
					</Button>
				</Col>

				<Col xl={3} className='item-cols'>
					<Button
						variant='light'
						bsPrefix='btn'
						className='switch-button'
						onClick={() => {
							update(!suspended, props.User.id);
						}}>
						<TableSubtitle>Suspender</TableSubtitle>
						<FormCheck
							className='switch-actual'
							type='switch'
							id={'custom-switch' + props.btnID}
							checked={suspended}
							onChange={() => setSuspended(suspended)}
						/>
					</Button>
				</Col>

				<Col xl={1} className='item-cols'>
					<Dropdown>
						<Dropdown.Toggle
							variant='none'
							id='dropdown-basic'
							bsPrefix='teste'>
							<i class='fas fa-ellipsis-v'></i>
						</Dropdown.Toggle>

						<Dropdown.Menu>
							<Dropdown.Item onClick={() => setShowDetails(true)}>
								<i class='far fakeicon'></i>Ver Mais
							</Dropdown.Item>

							<Dropdown.Item
								onClick={() => {
									setShowEdit(true);
								}}>
								<i class='far fa-edit'></i>Editar
							</Dropdown.Item>
							<Dropdown.Item onClick={() => del()}>
								<i class='fas fa-trash'></i>Excluir
							</Dropdown.Item>
						</Dropdown.Menu>
						<Details
							show={showDetails}
							onHide={() => setShowDetails(false)}
							UserInfo={props.User}
						/>
						<EditUser
							show={showEdit}
							onHide={() => {
								setShowEdit(false);
								cont = Math.random();
								props.setUpdate(cont);
							}}
							UserInfo={props.User}
						/>
						<MyCenteredModal
							show={showDate}
							onHide={() => {
								setShowDate(false);
								cont = Math.random();
								props.setUpdate(cont);
							}}
							UserInfo={props.User}
						/>
					</Dropdown>
				</Col>
			</Row>
		);
	};

	return (
		<ListGroup className=''>
			<ListGroupItem className='list-header-container'>
				<Row>
					<Col xl={3} className='header-cols'>
						<TableTitle>USUÁRIO</TableTitle>
					</Col>

					<Col xl={2} className='header-cols'>
						<TableTitle>INICIO DE CONTRATO</TableTitle>
					</Col>

					<Col xl={3} className='header-cols'>
						<TableTitle>VENCIMENTO DE CONTRATO</TableTitle>
					</Col>

					<Col xl={3} className='header-cols'>
						<TableTitle>SERVIÇO</TableTitle>
					</Col>

					<Col xl={1} className='header-cols'></Col>
				</Row>
			</ListGroupItem>
			{/* {renderItem(props.Data)} */}
			{pagination(items, 5, page).map((ele, idx) => (
				<ListGroupItem key={idx} className='list-item-container'>
					<Item
						User={ele}
						btnID={idx}
						setModalShow={props.setModalShow}
						setUpdate={props.setUpdate}
					/>
				</ListGroupItem>
			))}

			{/* show pagination */}
			{items.length > 5 ? (
				<div className='pagination'>
					<button disabled={page === 1} onClick={(e) => setPage(page - 1)}>
						anterior
					</button>
					<button
						disabled={page === numberOfPages(items)}
						onClick={(e) => setPage(page + 1)}>
						próximo
					</button>
				</div>
			) : null}
		</ListGroup>
	);
};
