// https://redux-saga.js.org/docs/advanced/Channels/#using-channels-to-communicate-between-sagas
import { eventChannel } from 'redux-saga';
import { call, fork, take, put, takeLatest, all } from 'redux-saga/effects';
import { addError } from './actions';

function watchErrors(socket) {
	return eventChannel((emitter) => {
		socket.on('reportError', (error) => {
			console.log(error);
			emitter(addError(error));
		});
		return () => {
			socket.close();
		};
	});
}

function* _connectErrors({ socket }) {
	if (!socket) return;
	const channel = yield call(watchErrors, socket);
	while (true) {
		try {
			let action = yield take(channel);
			yield put(action);
			yield fork(_connectErrors, socket);
		} catch (error) {
			console.log('saga', 'error list', error);
			channel.close();
		}
	}
}

function* _disconnectErrors({ socket }) {
	if (!socket) return;
	socket.disconnect(true);
}

export default all([
	takeLatest('@errors/CONNECT_ERRORS', _connectErrors),
	takeLatest('@errors/DISCONNECT_ERRORS', _disconnectErrors),
]);
