const INITIAL_STATE = {
	colors: {
		Select: '#1664C5',
		Background: '#FFFFFF',
		Buttons1: '#24446C',
		Buttons2: 'yellow',
		Text1: '#121212',
		Text2: '#FFFFFF',
	},
	isLoading: false,
};

function theme(state = INITIAL_STATE, action) {
	switch (action.type) {
		case '@theme/FETCH_THEME':
			return {
				...state,
				isLoading: true,
			};

		case '@theme/FETCH_THEME_SUCCESS':
			return {
				colors: action.theme,
				isLoading: false,
			};

		case '@theme/SET_THEME':
			return {
				...state,
				isLoading: true,
			};

		case '@theme/SET_THEME_SUCCESS':
			return {
				colors: action.theme,
				isLoading: false,
			};

		case '@theme/RESET_THEME':
			return INITIAL_STATE;

		default:
			return state;
	}
}

export default theme;
