import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import * as api from '../Services/api';
import { OverlayTrigger, Tooltip, Spinner } from 'react-bootstrap';

// styles
const Spacer = styled.div`
	width: 100%;
	padding: 16px 15px 16px 60px;
	@media (max-width: 768px) {
		padding: 16px 0 !important;
	}
`;

const Container = styled.div`
	display: flex;
	flex-direction: column;
	background: #e9e9e9;
	border-radius: 5px;
	padding: 16px;
	font-family: Poppins, sans-serif;
	color: ${({ theme }) => theme.colors.Text1};

	> span {
		font-weight: 600;
		padding-bottom: 8px !important;
	}

	> div {
		display: grid;
		grid-gap: 16px;
		grid-template-columns: repeat(auto-fill, 80px);
	}
`;

const Preset = styled.div`
	background: ${({ theme }) => theme.colors.Background};
	padding: 8px;
	border-radius: 5px;
	text-align: center;
	cursor: pointer;
	border: solid 2px
		${({ theme, selected }) => (selected ? theme.colors.Select : 'transparent')};
`;

// main component
export const PresetsControl = ({ transmitionStatus, cameraId }) => {
	const [isLoading, setIsLoading] = useState(false);
	const [presets, setPresets] = useState([]);
	const [presetSelected, setPresetSelected] = useState(1);

	// ! update presets when transmition start
	useEffect(() => {
		async function loadPresets() {
			setIsLoading(true);
			if (transmitionStatus) {
				const res = await api.getPresets(cameraId);
				// const res = teste; // teste
				if (!res.error) {
					setPresets(parserPresetsStringToArray(res));
				}
			}
			setIsLoading(false);
		}
		loadPresets();
	}, [transmitionStatus, cameraId]);

	// string to array of objects
	const parserPresetsStringToArray = (string) => {
		const presetArray = [];
		const _array = string.split(/\r\n|\n|\r/);
		// console.log('response array ->', _array);
		let counter = 0;
		let obj = {};
		_array.forEach((ele) => {
			const idx = Number(
				ele.substring(ele.indexOf('[') + 1, ele.lastIndexOf(']'))
			);
			if (idx === counter) {
				if (Object.keys(obj).length === 0) {
					return (obj['index'] = idx + 1);
				}
				const name = ele.substring(ele.indexOf('Name=') + 5, ele.length);
				obj['name'] = name;
				presetArray.push(obj);
				counter++;
				return (obj = {});
			}
			return;
		});
		// return a array of objects
		return presetArray;
	};

	const onSelectPrest = async (index) => {
		if (index) {
			const res = await api.moveCamera(cameraId, index);
			if (!res.error) {
				setPresetSelected(index);
			}
		}
	};

	if (!transmitionStatus) return null;

	return (
		<Spacer>
			<Container>
				<span>Controle da câmera</span>
				{isLoading ? (
					<Spinner animetion='border' size='sm' />
				) : (
					<div>
						{presets.map((ele, idx) => (
							<OverlayTrigger
								key={idx}
								placement='top'
								overlay={
									<Tooltip style={{ fontFamily: 'inherit' }}>
										{ele.name}
									</Tooltip>
								}>
								<Preset
									selected={presetSelected === ele.index}
									onClick={(e) => onSelectPrest(ele.index)}>
									{ele.index}
								</Preset>
							</OverlayTrigger>
						))}
					</div>
				)}
			</Container>
		</Spacer>
	);
};

const mapStateToProps = (state) => ({
	cameraId: state.camera.selected.id,
	transmitionStatus: state.streaming.on,
});

export default connect(mapStateToProps)(PresetsControl);

